import Player, { PlayerAction, PlayerStatus } from "./player";
import PlayerControl from "./player-control";
import Client from "./multiplayer/client";
import Input, { InputData } from "./input";
import { PlayerInfo, PlayerInput } from "./multiplayer/shared/IMessageData";
import EventManager from "../core/event-manager";
import Game from "./game";
import {ConfigGame} from "./ConfigGame";
import Tutorial, { TUTORIAL_SUB_STATE, TUTORIAL_STATE } from "./tutorial";
import PlayerRef, { DesignType } from "../player-ref";
import CameraControl from "./camera-control";
import SoundManager from "../core/sound-manager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PlayerControlLocal extends PlayerControl {

    private client: Client;

    private lastInputTime: number = null;
    private lastInputSeq: number = 0;
    private pendingInputs: PlayerInput[] = [];

    init(client: Client) {
        this.client = client;
        Input.instance.clear();
    }

    start() {
        super.start();
        //this.player.directionSprite.node.active = true;     // active indicator direction sprite
        // default data
        EventManager.instance.dispatch(Player.EVENT_LOCAL_PLAYER_EXP_CHANGE, 1, 0);
        if (this.client != null)
        this.player.updateAction(PlayerAction.Idle, 90);
    }

    setServerData(playerInfo: PlayerInfo) {
        if(this.client != null)
        {
        super.setServerData(playerInfo);
        if (this.player.level != playerInfo.level || this.player.exp != playerInfo.exp) {
            EventManager.instance.dispatch(Player.EVENT_LOCAL_PLAYER_EXP_CHANGE, playerInfo.level, playerInfo.exp);
        }
        }
    }

    handleDeath(): boolean {
        return true;    // do nothing for local player
    }

    update(dt: number) {
        // fixing #10591055
        // if (!Game.isTutorial)
        //     if (this.player.isDeath()) return;

        if (this.client != null)
            this.processServerData();
            
        this.processInput();
    }

    processServerData() {
        while (this.serverData.length > 0) {
            let data = this.serverData.shift();
            this.player.updatePlayerInfo(data.x, data.y, data.opacity, data.health, data.level, data.exp, data.death, data.vulnerable, data.bombable, data.skillable, data.buffs);
            this.player.cskin = data.cskin;
            this.player.healthBar.updateNotify();
            let lastProcessedInputSeq = data.lastInputSeq;
            this.player.setPosition(data.x, data.y);
            if (this.pendingInputs.length > 0) {
                let i = 0;
                while (i < this.pendingInputs.length) {
                    let input = this.pendingInputs[i];
                    if (input.seq <= lastProcessedInputSeq) {
                        this.pendingInputs.splice(i, 1);
                    } else {
                        this.applyInput(input);
                        i++;
                    }
                }
            } else {
                if (this.player.action != data.action || this.player.direction != data.direction) {
                    this.player.updateAction(data.action, data.direction);
                }
            }
        }
    }

    processInput() {
        let now = Date.now();
        let last = this.lastInputTime || now;
        let dt = (now - last);
        this.lastInputTime = now;

        let inputData = Input.instance.getData();
        if (inputData) {
            let playerInput: PlayerInput = {
                direction: inputData.direction,
                time: dt,
                seq: (this.lastInputSeq++ & 0xffff),
                fire: inputData.fire,
                fire_dir: inputData.fire_dir,
                fire_range: inputData.fire_range
            }
            if (this.validateInput(playerInput)) {
                // send input to server
		if (this.client != null)
		{
                this.client.sendAction(playerInput);
                this.applyInput(playerInput);
                this.pendingInputs.push(playerInput);

                }
                else
                {
                this.applyInput(playerInput);
                this.pendingInputs.push(playerInput);
                }
            }
        }
    }

    validateInput(input: PlayerInput) {
        if (input.time < 40) return true;
        return false;
    }

    applyInput(input: PlayerInput) {
        // Nhan rem
        //let playerAction: PlayerAction;
        // Nhan change
        let playerAction: number =  -1;
        let direction: number = this.player.direction;
        let deltaPos: cc.Vec2 = null;
        // move state
        if (input.direction >= 0) {
            playerAction = PlayerAction.Move;
            direction = cc.misc.clampf(input.direction, 0, 360);

            let dx = 0;
            let dy = 0;
                let angleRad = direction * Math.PI / 180;         // to rads


                let playerSpeed;
                if (Game.isTutorial)
                    playerSpeed = ConfigGame.PLAYER_SPEED;
                else
                    playerSpeed = Game.instance.playerSpeed;
				
                
            dx = Math.cos(angleRad) * playerSpeed * input.time / 1000.0;
            dy = Math.sin(angleRad) * playerSpeed * input.time / 1000.0;

            deltaPos = cc.v2(Math.round(dx), Math.round(dy));

        } else if (input.direction < 0) {
            playerAction = PlayerAction.Idle;
        }   // incase NaN -> input not include direction value

        if (Game.isTutorial)
        {
            // cc.log("Nhan --> input.fire_dir = " + input.fire_dir);
            // cc.log("Nhan --> input.fire_range = " + input.fire_range);
            // if (input != null && input.fire_dir != null && input.fire_range != null)
            !input.fire && Tutorial.instance.updateLocalTarget(input.fire_dir, input.fire_range);
        }
        else
        {
            if(Game.USE_DYNAMIC_BOMB)
                !input.fire && Game.instance.updateLocalTarget(input.fire_dir, input.fire_range);
        }
        // fire state
        if (input.fire) {
            if (this.player.bombable && input.fire == 1) {
                playerAction = PlayerAction.Fire;
                if (Game.isTutorial)
                {
                    //Tutorial.instance.updateBombSkill(2);
                    if (this.player.m_bomb && this.player.m_bomb.collider)
                        this.player.m_bomb.collider.tag = 2;
                }
            } 
            else if (this.player.skillable && input.fire == 2) {
                // Nhan note: change active skill (throwing special bomb) to passive skill (speed, shield, invisible)
                // playerAction = PlayerAction.Fire;
                if (this.player.cskin == DesignType.Fire) {
                    //cc.log("Nhan --> input.fire --> DesignType.Fire");
                    let angleRad = direction * Math.PI / 180;
                    let dx = Math.cos(angleRad) * 160;
                    let dy = Math.sin(angleRad) * 160;
                    deltaPos = cc.v2(Math.round(dx), Math.round(dy));
                    if (Game.isTutorial)
                    {
                        this.player.collider.enabled =false;
                        Tutorial.instance.BOTplayer.collider.enabled = false;
                    }

                } else if (this.player.cskin == DesignType.Ice) {
                    this.player.effectsNode.children[0].active = true;
                } else if (this.player.cskin == DesignType.Grass) {
                    this.player.node.opacity = 100;
                }

                if (Game.isTutorial)
                {
                    if (this.player.m_bomb && this.player.m_bomb.collider)
                        this.player.m_bomb.collider.tag = 3;
                    if ( Tutorial.tutorial_status == TUTORIAL_STATE.Tutorial_skill
                        && Tutorial.tutorial_sub_status == TUTORIAL_SUB_STATE.Tutorial_checkdoAction
                        && Tutorial.instance.performAction == 1
                        )
                        {
                            Tutorial.instance.performAction = 2;
                        }
                    //Tutorial.instance.TutorialPlayer.updatePlayerInfo(0, 0,255, 70, 1, 0, false, false, true, true, []);
                }
                //this.player.activeEffect(+PlayerRef.current.user + 5);

            }
        }

        // apply to local player
        //if (playerAction != null) 
		{
            if (this.player.action != playerAction || this.player.direction != direction) {
                this.player.updateAction(playerAction, direction);
            }
            
            // Nhan add
            //if (deltaPos != null && this.player.action == PlayerAction.Move) {
            if (deltaPos != null) {
                if (Game.isTutorial)
                {
                    let pos = this.player.node.position.add(deltaPos);
                    if (pos.x < -460)
                        pos.x= -460;
                    if (pos.x > 460)
                        pos.x= 460;

                    if (pos.y < -300)
                        pos.y= -300;
                    if (pos.y > 300)
                        pos.y= 300;

                    this.player.setPosition(pos.x, pos.y);
                }
                else
                {
                    let pos = this.player.node.position.add(deltaPos);
                    if (pos.x - ConfigGame.PLAYER_HALF_W < 0) pos.x = ConfigGame.PLAYER_HALF_W;
                    if (pos.x + ConfigGame.PLAYER_HALF_W > ConfigGame.WORLD_WIDTH) pos.x = ConfigGame.WORLD_WIDTH - ConfigGame.PLAYER_HALF_W;
                    if (pos.y - ConfigGame.PLAYER_HALF_H < 0) pos.y = ConfigGame.PLAYER_HALF_H;
                    if (pos.y + ConfigGame.PLAYER_HALF_H > ConfigGame.WORLD_HEIGHT) pos.y = ConfigGame.WORLD_HEIGHT - ConfigGame.PLAYER_HALF_H;
                    this.player.setPosition(pos.x, pos.y);      // FIXME: node pos or serverpos???
                }
            }
        }
    }

}