
import Game from "../game";
import Player from "../player";
import { LbInfo } from "../multiplayer/shared/IMessageData";

const { ccclass, property } = cc._decorator;

@ccclass
export default class MiniMap extends cc.Component {

    @property(cc.Prefab)
    indicatorPrefab: cc.Prefab = null;

    @property({type: [cc.SpriteFrame]})
    ranks: cc.SpriteFrame[] = [];

    

    private ratio: number = 130 / 960; //@ ratio = mini-map radius * 2 / view height
    private radius: number = 55;
    indicators: { [key: string]: cc.Node } = {};

    start() {
        let ind = cc.instantiate(this.indicatorPrefab);
        ind.color = cc.Color.BLUE;
        ind.setPosition(cc.Vec2.ZERO); //@ local player indicator always at center
        this.node.addChild(ind);
    }

    updatePlayersPosition(players: { [key: string]: Player }) {
        //@ update player's position on mimi-map
        for (let id in players) {

            let dx = (players[id].node.position.x - Game.instance.localPlayer.node.position.x) * this.ratio;
            let dy = (players[id].node.position.y - Game.instance.localPlayer.node.position.y) * this.ratio;
            let x = dx, y = dy;
            if(dx * dx + dy * dy > this.radius * this.radius) {
                let rad = Math.atan2(dy, dx);
                x = Math.cos(rad) * this.radius;
                y = Math.sin(rad) * this.radius;
            }

            if (!this.indicators[id]) {
                let ind = cc.instantiate(this.indicatorPrefab);
                //ind.color = cc.Color.RED;
                this.node.addChild(ind);
                this.indicators[id] = ind;
            }
            this.indicators[id].setPosition(x, y);
        }
    }

    updatePlayersDisplay(data: LbInfo[]) {
        data.forEach((item, idx) => {
            let rank = idx + 1;
            if (this.indicators[item.id]) {
                if (rank > 3) {
                    this.indicators[item.id].setContentSize(8, 8);
                } else  {
                    //this.indicators[item.id].color = cc.Color.WHITE;
                    this.indicators[item.id].setContentSize(16, 16);
                    this.indicators[item.id].getComponent(cc.Sprite).spriteFrame = this.ranks[rank-1];
                }
            }
        });
    }

    removeFromMap(leftPlayerId: string[]) {
        leftPlayerId.forEach(id => {
            if(this.indicators[id]) {
                this.indicators[id].removeFromParent();
                delete this.indicators[id];
            }
        })
    }
}
