import Player from "../player";

const { ccclass, property } = cc._decorator;

@ccclass
export default class HealthBar extends cc.Component {

    @property(cc.Node)
    private segments: cc.Node = null;

    @property({type: [cc.SpriteFrame]})
    healthColors: cc.SpriteFrame[] = [];

    private healthBar: cc.ProgressBar;
    private curColor = 0; //@ green
    //private colorFrames: {[key: string]: cc.SpriteFrame} = {}


    start() {
        this.healthBar = this.getComponent(cc.ProgressBar);
        this.curColor = 0;
        // ["red", "green"].forEach(color => {
        //     cc.loader.loadRes("textures/blood_" + color + ".png", function(err, data){
        //         this.colorFrames[color] = new cc.SpriteFrame(data);
        //     }.bind(this));
        // })
    }

    updateHealth(level: number, current: number) {
        if(!this.healthBar) return;
        
        this.healthBar.progress = current / Player.getHealth(level);
        
        // segs
        //let segno = Math.ceil(total / 35);
        if (level >= 2) {
            let cursegno = this.segments.childrenCount;
            for(let i = cursegno; i < level; i++) {
                let seg = cc.instantiate(this.segments.children[0]);
                this.segments.addChild(seg);
            }
        }
    }

    // use to update health color notify on local player
    updateNotify() {
        if (this.healthBar.progress < 0.3 && this.curColor == 0) {
            this.curColor = 1;
            this.healthBar.getComponentInChildren(cc.Sprite).spriteFrame = this.healthColors[this.curColor];
        } else if (this.healthBar.progress == 1 && this.curColor == 1) {
            this.curColor = 0;
            this.healthBar.getComponentInChildren(cc.Sprite).spriteFrame = this.healthColors[this.curColor];
        }
    }
}