import NetComponent from "./net-component";
import EventManager from "../core/event-manager";
import { ExpBubleInfo } from "./multiplayer/shared/IMessageData";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ExpBuble extends NetComponent {
    @property({type: cc.SpriteFrame})
    frames: cc.SpriteFrame[] = []

    start() {
        //this.node.zIndex = -1;
    }

    init(data: ExpBubleInfo) {
        super.init(data);
        this.node.opacity = data.opacity;
        this.node.setPosition(data.x, data.y);
        this.node.getComponent(cc.Sprite).spriteFrame = this.frames[data.type];
    }

    setStateData(data: ExpBubleInfo) {
        super.setStateData(data);
        this.node.opacity = data.opacity;
        this.death = data.death;
        this.node.getComponent(cc.Sprite).spriteFrame = this.frames[data.type];
    }

    onDeath(timeout: boolean) {
        EventManager.instance.dispatch(NetComponent.EVENT_NET_ENTITY_DEATH, this.id);

        if(timeout) {
            this.node.removeFromParent();
        } else {
            // anim death
            this.node.runAction(cc.sequence(
                cc.fadeOut(0.2),
                cc.callFunc(() => {
                    this.node.removeFromParent();
                })
            ))
        }
    }

}