//cpp style - protected, private
//ts style  - public

import Client, { ClientListener } from "../multiplayer/client";
import Game from "../game";
import { GameInfo, GameInitInfo, GameData, LbInfo, NotifyInfo, DeathInfo } from "../multiplayer/shared/IMessageData";

const {ccclass, property} = cc._decorator;

@ccclass
export abstract class ClientRequest implements ClientListener
{
    protected static ms_client: Client = null;

    protected StartRequest(event: string, onRequestFinished: (err, res) => void) { if(ClientRequest.ms_client) ClientRequest.ms_client.sendRequest(event, onRequestFinished) }

    //hoang.tuduc - game listener
    onGameInfo(info: GameInfo) { console.log("ClientRequest - onGameInfo"); }//if(Game.instance) Game.instance.onGameInfo(info); }
    onJoinGame(info: GameInitInfo) { }//if(Game.instance) Game.instance.onJoinGame(info); }
    onServerData(data: GameData) { }//if(Game.instance) Game.instance.onServerData(data); }
    onError(error) { }//if(Game.instance) Game.instance.onError(error); }
    onDeath(data: DeathInfo) { }//if(Game.instance) Game.instance.onDeath(data); }
    onNotiMessage(msg: NotifyInfo) { }//if(Game.instance) Game.instance.onNotiMessage(msg); }
    onLeaderboardChange(data: LbInfo[]) { }//if(Game.instance) Game.instance.onLeaderboardChange(data); }

    // abstract OnRequestFinished(err, res: any): void;
}
