import Game from "./game";
import NetComponent from "./net-component";
import EventManager from "../core/event-manager";
import { BombInfo, BombExtraInfo } from "./multiplayer/shared/IMessageData";
import SoundManager from "../core/sound-manager";
import {ConfigGame} from "./ConfigGame";
import {Utils} from "../utils";
import Player from "./player";
import {PlayerAction} from "./player";
import Tutorial, { TUTORIAL_SUB_STATE, TUTORIAL_STATE } from "./tutorial";

export enum Effect {
    None = 0,
    //@ immune status
    Immune,
    //@ leader board
    DmgX2,
    DefX2,
    ExpX2,
    //@ battle effect
    Burn,
    Slow,
    Bind, 
}


const {ccclass, property} = cc._decorator;

@ccclass
export default class Bomb extends NetComponent {

    ownerId: number = 0;
    targetPos: cc.Vec2 = null;
    exploded: boolean = false;
    timePercent: number = 0;
    skin: number = 0;

    // Nhan add
    collider:cc.CircleCollider = null;

    fire_angle: number = 0;
    fire_range: number = 0;

    owner: Player = null;
    applyDamge: boolean = false;        // delay after bomb explore to apply damge
    damage: number = ConfigGame.BOMB_BASE_DAMGE;       // bomb damge
    effect: number; // effect for special skill or skin for normal attack
    skill: {effect: Effect, cooldown: number} = null;
    opacity: number = 255;
    private startPos: cc.Vec2 = null;
    private endPos: cc.Vec2 = null;
    private timeFlight: number = ConfigGame.BOMB_LENGTH / ConfigGame.BOMB_SPEED;
    private time: number = 0;
    private extraData = null;

    private _exploded = false;
    private _flying = true;
    private animation: cc.Animation;
    private explosion_anim: string[] = ['fire_explosion', 'ice_explosion', 'grass_explosion'];
    private fly_anim: string[] = ['fire_fly', 'ice_fly', 'grass_fly'];

    onCollisionEnter(other, self) {
        //console.log("Bomb Currently colliding");  
        if (this.collider)
        {
            this.collider.radius = 0;
            this.collider.enabled = false;
        }
    }
    onCollisionExit(other,self){
        //console.log("Bomb Done colliding");
        if (this.collider)
        {
            this.collider.radius = 0;
            this.collider.enabled = false;
        }
    }

    start() {
        this.animation = this.getComponent(cc.Animation);
    }

    init(data: BombInfo) {
        super.init(data);
        this.node.opacity = data.opacity;
        this.timePercent = data.percent;
        this.exploded = data.exploded;
        this.skin = data.skin;
        this.node.setPosition(data.x, data.y);
        if (Game.isTutorial)
            this.setStartPos(data.x, data.y);
    }

    setStateData(data: BombInfo) {
        super.setStateData(data);
        this.node.setPosition(data.x, data.y);
        if (Game.isTutorial)
			this.setStartPos(data.x, data.y);
        this.node.opacity = data.opacity;
        this.timePercent = data.percent;
        this.exploded = data.exploded;
        this.skin = data.skin;
    }

    setExtraData(data: BombExtraInfo) {
        this.ownerId = data.owner;
        this.setTargetPos(data.endX, data.endY);
    }

    private setTargetPos(x: number, y: number) {
        this.targetPos = cc.v2(x, y);
        //let direction = this.targetPos.sub(this.node.position);
        //let angle = Math.atan2(direction.y, direction.x) * 180 / Math.PI;
        //this.node.rotation = 360 - angle + 180;
    }

    update(dt: number) {
        if (Game.isTutorial)
        {
            if (this.endPos != null)
            {
                if (this.exploded == false) {
                    if (this.time >= (this.timeFlight/2) 
                        && Tutorial.tutorial_status == TUTORIAL_STATE.Tutorial_skill
                        && Tutorial.tutorial_sub_status <= TUTORIAL_SUB_STATE.Tutorial_checkdoAction
                        && Tutorial.instance.performAction <= 1
                        )
                        {
                            // cc.log("Nhan --> Pause bomb ");
                            // this.animation.pause(this.fly_anim[this.skin]);
                            if (Tutorial.instance.performAction == 0)
                                Tutorial.instance.performAction = 1;
                            return;
                        }
                    this.time += dt;
                    if (this.time >= this.timeFlight) 
					{
                        this.exploded = true;
                    }
                }
                let pos = this.startPos.lerp(this.endPos, this.time / this.timeFlight);
                this.node.x = Math.round(pos.x);
                this.node.y = Math.round(pos.y);

                this.timePercent = (this.time / this.timeFlight) * 100;
            }
        }
        if (this._flying) {
            this.animation.play(this.fly_anim[this.skin]);
            this._flying = false;
        }

        // scale -> simulate bomb fly
        let scale = cc.misc.lerp(0.75, 1, cc.misc.clamp01(this.timePercent > 50 ? (100 - this.timePercent)/50 : this.timePercent/50));
        this.animation.node.setScale(scale);
        
        if(this.exploded && this._exploded == false) {
            this._exploded = true;
            this.animation.node.setScale(0.75);
            this.animation.play(this.explosion_anim[this.skin]);
            this.animation.on('finished', this.onAnimationEvent, this);  
            if (this.collider)
            {
                //cc.log("Expand radius for check collision");
                this.collider.enabled = true;
                this.collider.radius = 50;
            }
        }
    }

    onDeath(timeout: boolean) {
        if (!Game.isTutorial)
        {
        EventManager.instance.dispatch(NetComponent.EVENT_NET_ENTITY_DEATH, this.id);
        this.node.removeFromParent();
        }
    }

    onAnimationEvent(type: string, state: cc.AnimationState) {
        if(type === 'finished') {            
            if(state.name.indexOf('explosion') > 0) {
                this.death = true;
                if (Game.isTutorial)
                {
                    this.endPos = null;
                    this.time = 0;
                    this._flying = true;
                    this.node.opacity = 0;
                    this.owner.bombable = true;
                }
            }
            if (Game.isTutorial) {
                Tutorial.instance.updateTutorialStatus();
            }
        }
    }


    // Nhan add
    setStartPos(x: number, y: number)
    {
        if (this.collider)
        {
            this.collider.enabled = false;
            this.collider.radius = 0;
        }
        this.startPos = cc.v2(x, y + ConfigGame.PLAYER_HEIGHT);        
    }
    setEndPos(x: number, y: number)
    {
        //let x = Math.round(Math.cos(this.owner.fireInfo.angle) * owner.fireInfo.range);
        //let y = Math.round(Math.sin(owner.fireInfo.angle) * owner.fireInfo.range);
        if (this.collider)
        {
            this.collider.enabled = false;
            this.collider.radius = 0;
        }
        this.endPos = cc.v2(x, y);
    }
    getEndPos(): cc.Vec2
    {
        return this.endPos;
    }
    updateEndPos()
    {
        //let x = Math.round(Math.cos(this.fire_angle) * this.fire_range);
        //let y = Math.round(Math.sin(this.fire_range) * this.fire_range);
        this.endPos = cc.v2(this.startPos.x , this.startPos.y + + ConfigGame.PLAYER_HEIGHT);
    }

    setExplodeStatus(is_exploded: boolean)
    {
        this._exploded = is_exploded;
    }
    setOwnerPlayer(m_player: Player)
    {
        this.owner = m_player;
    }

}