import { SequenceNodeShowSelectSkinPopup } from './SequenceNodeShowSelectSkinPopup'
import { SequenceNodeShowPopupState } from './SequenceNodeShowPopup';
import PlayerRef from "../../player-ref";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SequenceNodeShowServerSelectionPopup extends SequenceNodeShowSelectSkinPopup
{
    public ShowPopup()
    {//console.log("SequenceNodeShowServerSelectionPopup - ShowPopup");
        this.main_menu.showServerSelectionPopup();
    }

    protected CreateConnection()
    {
        super.CreateConnection();

        var clickEventHandler = new cc.Component.EventHandler();
        clickEventHandler.target = this.node; // This node is the node to which your event handler code component belongs
        clickEventHandler.component = "SequenceNodeShowServerSelectionPopup";// This is the code file name
        clickEventHandler.handler = "OnJoinButtonPressed";

        this.m_closeEvent = this.m_closeButton.clickEvents.push(clickEventHandler);
    }

    public OnCloseButtonPressed(event, customEventData)
    {
        super.OnCloseButtonPressed(event, customEventData);
        PlayerRef.current.first_launch_step = 2;
    }

    public OnJoinButtonPressed(event, customEventData)
    {
        PlayerRef.current.first_launch_step = 2;
    }
}
