// Typescript port from https://github.com/inolen/bit-buffer/blob/master/bit-buffer.js

/**********************************************************
 *
 * BitView
 *
 * BitView provides a similar interface to the standard
 * DataView, but with support for bit-level reads / writes.
 *
 **********************************************************/
export class BitView {
    // Used to massage fp values so we can operate on them at the bit level.
    private static _scratch = new DataView(new ArrayBuffer(8));

    private _view: Uint8Array;

    public bigEndian: boolean;

    constructor(source: ArrayBuffer | Buffer, byteOffset?: number, byteLength?: number) {
        let isBuffer = source instanceof ArrayBuffer ||
            (typeof Buffer !== 'undefined' && source instanceof Buffer);

        if (!isBuffer) {
            throw new Error('Must specify a valid ArrayBuffer or Buffer.');
        }

        byteOffset = byteOffset || 0;
        byteLength = byteLength || (source as ArrayBuffer).byteLength /* ArrayBuffer */ || (source as Buffer).length /* Buffer */;

        this._view = new Uint8Array(source, byteOffset, byteLength);

        this.bigEndian = false;
    }

    get buffer(): ArrayBuffer | Buffer {
        return typeof Buffer !== 'undefined' ? Buffer.from(this._view.buffer) : this._view.buffer;
    }

    get byteLength(): number {
        return this._view.length;
    }

    public setBit(offset: number, on: boolean) {
        if (on) {
            this._view[offset >> 3] |= 1 << (offset & 7);
        } else {
            this._view[offset >> 3] &= ~(1 << (offset & 7));
        }
    }

    public getBits(offset: number, bits: number, signed?: boolean): number {
        let available = (this._view.length * 8 - offset);

        if (bits > available) {
            throw new Error('Cannot get ' + bits + ' bit(s) from offset ' + offset + ', ' + available + ' available');
        }

        let value = 0;
        for (let i = 0; i < bits;) {
            let remaining = bits - i;
            let bitOffset = offset & 7;
            let currentByte = this._view[offset >> 3];

            // the max number of bits we can read from the current byte
            let read = Math.min(remaining, 8 - bitOffset);

            let mask, readBits;
            if (this.bigEndian) {
                // create a mask with the correct bit width
                mask = ~(0xFF << read);
                // shift the bits we want to the start of the byte and mask of the rest
                readBits = (currentByte >> (8 - read - bitOffset)) & mask;

                value <<= read;
                value |= readBits;
            } else {
                // create a mask with the correct bit width
                mask = ~(0xFF << read);
                // shift the bits we want to the start of the byte and mask off the rest
                readBits = (currentByte >> bitOffset) & mask;

                value |= readBits << i;
            }

            offset += read;
            i += read;
        }

        if (signed) {
            // If we're not working with a full 32 bits, check the
            // imaginary MSB for this bit count and convert to a
            // valid 32-bit signed value if set.
            if (bits !== 32 && value & (1 << (bits - 1))) {
                value |= -1 ^ ((1 << bits) - 1);
            }

            return value;
        }

        return value >>> 0;
    }

    public setBits(offset: number, value: number, bits: number) {
        let available = (this._view.length * 8 - offset);

        if (bits > available) {
            throw new Error('Cannot set ' + bits + ' bit(s) from offset ' + offset + ', ' + available + ' available');
        }

        for (let i = 0; i < bits;) {
            let remaining = bits - i;
            let bitOffset = offset & 7;
            let byteOffset = offset >> 3;
            let wrote = Math.min(remaining, 8 - bitOffset);

            let mask, writeBits, destMask;
            if (this.bigEndian) {
                // create a mask with the correct bit width
                mask = ~(~0 << wrote);
                // shift the bits we want to the start of the byte and mask of the rest
                writeBits = (value >> (bits - i - wrote)) & mask;

                let destShift = 8 - bitOffset - wrote;
                // destination mask to zero all the bits we're changing first
                destMask = ~(mask << destShift);

                this._view[byteOffset] =
                    (this._view[byteOffset] & destMask)
                    | (writeBits << destShift);

            } else {
                // create a mask with the correct bit width
                mask = ~(0xFF << wrote);
                // shift the bits we want to the start of the byte and mask of the rest
                writeBits = value & mask;
                value >>= wrote;

                // destination mask to zero all the bits we're changing first
                destMask = ~(mask << bitOffset);

                this._view[byteOffset] =
                    (this._view[byteOffset] & destMask)
                    | (writeBits << bitOffset);
            }

            offset += wrote;
            i += wrote;
        }
    }

    public getBoolean(offset: number): boolean {
        return this.getBits(offset, 1, false) !== 0;
    }

    public getInt8(offset: number): number {
        return this.getBits(offset, 8, true);
    }

    public getUint8(offset: number): number {
        return this.getBits(offset, 8, false);
    }

    public getInt16(offset: number): number {
        return this.getBits(offset, 16, true);
    }

    public getUint16(offset: number): number {
        return this.getBits(offset, 16, false);
    }


    public getInt32(offset: number): number {
        return this.getBits(offset, 32, true);
    }

    public getUint32(offset: number): number {
        return this.getBits(offset, 32, false);
    }

    public getFloat32(offset: number): number {
        BitView._scratch.setUint32(0, this.getUint32(offset));
        return BitView._scratch.getFloat32(0);
    }

    public getFloat64(offset: number): number {
        BitView._scratch.setUint32(0, this.getUint32(offset));
        // DataView offset is in bytes.
        BitView._scratch.setUint32(4, this.getUint32(offset + 32));
        return BitView._scratch.getFloat64(0);
    }

    public setBoolean(offset: number, value: number) {
        this.setBits(offset, value ? 1 : 0, 1);
    }

    public setUint8(offset: number, value: number) {
        this.setBits(offset, value, 8);
    }
    public setInt8(offset: number, value: number) { this.setUint8(offset, value) }

    public setUint16(offset: number, value: number) {
        this.setBits(offset, value, 16);
    }
    public setInt16(offset: number, value: number) { this.setUint16(offset, value) }

    public setUint32(offset: number, value: number) {
        this.setBits(offset, value, 32);
    }
    public setInt32(offset: number, value: number) { this.setUint32(offset, value) }

    public setFloat32(offset: number, value: number) {
        BitView._scratch.setFloat32(0, value);
        this.setBits(offset, BitView._scratch.getUint32(0), 32);
    }

    public setFloat64(offset: number, value: number) {
        BitView._scratch.setFloat64(0, value);
        this.setBits(offset, BitView._scratch.getUint32(0), 32);
        this.setBits(offset + 32, BitView._scratch.getUint32(4), 32);
    }

    public getArrayBuffer(offset: number, byteLength: number): ArrayBuffer {
        let buffer = new Uint8Array(byteLength);
        for (let i = 0; i < byteLength; i++) {
            buffer[i] = this.getUint8(offset + (i * 8));
        }
        return buffer.buffer;
    }
}

/**********************************************************
 *
 * BitStream
 *
 * Small wrapper for a BitView to maintain your position,
 * as well as to handle reading / writing of string data
 * to the underlying buffer.
 *
 **********************************************************/
// utils function
let reader = function (name, size) {
    return function () {
        if (this._index + size > this._length) {
            throw new Error('Trying to read past the end of the stream');
        }
        let val = this._view[name](this._index);
        this._index += size;
        return val;
    };
};

let writer = function (name, size) {
    return function (value) {
        this._view[name](this._index, value);
        this._index += size;
    };
};

function readASCIIString(stream, bytes) {
    return readString(stream, bytes, false);
}

function readUTF8String(stream, bytes) {
    return readString(stream, bytes, true);
}

function readString(stream, bytes, utf8) {
    if (bytes === 0) {
        return '';
    }
    let i = 0;
    let chars = [];
    let append = true;
    let fixedLength = !!bytes;
    if (!bytes) {
        bytes = Math.floor((stream._length - stream._index) / 8);
    }

    // Read while we still have space available, or until we've
    // hit the fixed byte length passed in.
    while (i < bytes) {
        let c = stream.readUint8();

        // Stop appending chars once we hit 0x00
        if (c === 0x00) {
            append = false;

            // If we don't have a fixed length to read, break out now.
            if (!fixedLength) {
                break;
            }
        }
        if (append) {
            chars.push(c);
        }

        i++;
    }

    let string = String.fromCharCode.apply(null, chars);
    if (utf8) {
        try {
            return decodeURIComponent(escape(string)); // https://stackoverflow.com/a/17192845
        } catch (e) {
            return string;
        }
    } else {
        return string;
    }
}

function writeASCIIString(stream, string, bytes) {
    let length = bytes || string.length + 1;  // + 1 for NULL

    for (let i = 0; i < length; i++) {
        stream.writeUint8(i < string.length ? string.charCodeAt(i) : 0x00);
    }
}

function writeUTF8String(stream, string, bytes) {
    let byteArray = stringToByteArray(string);

    let length = bytes || byteArray.length + 1;  // + 1 for NULL
    for (let i = 0; i < length; i++) {
        stream.writeUint8(i < byteArray.length ? byteArray[i] : 0x00);
    }
}

function stringToByteArray(str) { // https://gist.github.com/volodymyr-mykhailyk/2923227
    let b = [], i, unicode;
    for (i = 0; i < str.length; i++) {
        unicode = str.charCodeAt(i);
        // 0x00000000 - 0x0000007f -> 0xxxxxxx
        if (unicode <= 0x7f) {
            b.push(unicode);
            // 0x00000080 - 0x000007ff -> 110xxxxx 10xxxxxx
        } else if (unicode <= 0x7ff) {
            b.push((unicode >> 6) | 0xc0);
            b.push((unicode & 0x3F) | 0x80);
            // 0x00000800 - 0x0000ffff -> 1110xxxx 10xxxxxx 10xxxxxx
        } else if (unicode <= 0xffff) {
            b.push((unicode >> 12) | 0xe0);
            b.push(((unicode >> 6) & 0x3f) | 0x80);
            b.push((unicode & 0x3f) | 0x80);
            // 0x00010000 - 0x001fffff -> 11110xxx 10xxxxxx 10xxxxxx 10xxxxxx
        } else {
            b.push((unicode >> 18) | 0xf0);
            b.push(((unicode >> 12) & 0x3f) | 0x80);
            b.push(((unicode >> 6) & 0x3f) | 0x80);
            b.push((unicode & 0x3f) | 0x80);
        }
    }

    return b;
}

export class BitStream {
    private _index: number;
    private _startIndex: number;
    private _length: number;
    private _view: BitView;

    constructor(source: ArrayBuffer | Buffer | BitView, byteOffset?: number, byteLength?: number) {
        let isBuffer = source instanceof ArrayBuffer ||
            (typeof Buffer !== 'undefined' && source instanceof Buffer);

        if (!(source instanceof BitView) && !isBuffer) {
            throw new Error('Must specify a valid BitView, ArrayBuffer or Buffer');
        }

        if (isBuffer) {
            this._view = new BitView((source as ArrayBuffer | Buffer), byteOffset, byteLength);
        } else {
            this._view = source as BitView;
        }

        this._index = 0;
        this._startIndex = 0;
        this._length = this._view.byteLength * 8;
    }

    get index(): number {
        return this._index - this._startIndex;
    }
    set index(val: number) {
        this._index = val + this._startIndex;
    }

    get length(): number {
        return this._length - this._startIndex;
    }
    set length(val: number) {
        this._length = val + this._startIndex;
    }

    get bitsLeft(): number {
        return this._length - this._index;
    }

    get byteIndex() {
        // Ceil the returned value, over compensating for the amount of
        // bits written to the stream.
        return Math.ceil(this._index / 8);
    }
    set byteIndex(val: number) {
        this._index = val * 8;
    }

    get buffer(): Buffer | ArrayBuffer {
        return this._view.buffer;
    }

    get view(): BitView {
        return this._view;
    }

    get bigEndian() {
        return this._view.bigEndian;
    }
    set bigEndian(val: boolean) {
        this._view.bigEndian = val;
    }

    public readBits(bits: number, signed?: boolean): number {
        let val = this._view.getBits(this._index, bits, signed);
        this._index += bits;
        return val;
    }

    public writeBits(value: number, bits: number) {
        this._view.setBits(this._index, value, bits);
        this._index += bits;
    }

    public readBoolean: () => boolean = reader('getBoolean', 1);
    public readInt8: () => number = reader('getInt8', 8);
    public readUint8: () => number = reader('getUint8', 8);
    public readInt16: () => number = reader('getInt16', 16);
    public readUint16: () => number = reader('getUint16', 16);
    public readInt32: () => number = reader('getInt32', 32);
    public readUint32: () => number = reader('getUint32', 32);
    public readFloat32: () => number = reader('getFloat32', 32);
    public readFloat64: () => number = reader('getFloat64', 64);

    public writeBoolean: (value: boolean) => void = writer('setBoolean', 1);
    public writeInt8: (value: number) => void = writer('setInt8', 8);
    public writeUint8: (value: number) => void = writer('setUint8', 8);
    public writeInt16: (value: number) => void = writer('setInt16', 16);
    public writeUint16: (value: number) => void = writer('setUint16', 16);
    public writeInt32: (value: number) => void = writer('setInt32', 32);
    public writeUint32: (value: number) => void = writer('setUint32', 32);
    public writeFloat32: (value: number) => void = writer('setFloat32', 32);
    public writeFloat64: (value: number) => void = writer('setFloat64', 64);

    public readASCIIString(length?: number): string {
        return readASCIIString(this, length);
    }

    public readUTF8String(length?: number): string {
        return readUTF8String(this, length);
    }

    public writeASCIIString(data: string, length?: number) {
        writeASCIIString(this, data, length);
    }

    public writeUTF8String(data: string, length?: number) {
        writeUTF8String(this, data, length);
    }

    public readBitStream(bitLength: number): BitStream {
        let slice = new BitStream(this._view);
        slice._startIndex = this._index;
        slice._index = this._index;
        slice.length = bitLength;
        this._index += bitLength;
        return slice;
    }

    public writeBitStream(stream: BitStream, length?: number, offset?: number) {
        if (!length) {
            length = stream.bitsLeft;
        }

        if(typeof(offset) === 'number') {
            stream.index = offset;
        }

        let bitsToWrite;
        while (length > 0) {
            bitsToWrite = Math.min(length, 32);
            this.writeBits(stream.readBits(bitsToWrite), bitsToWrite);
            length -= bitsToWrite;
        }
    }

    public readArrayBuffer(byteLength: number): ArrayBuffer {
        let buffer = this._view.getArrayBuffer(this._index, byteLength);
        this._index += (byteLength * 8);
        return buffer;
    }

    public writeArrayBuffer(buffer: ArrayBuffer, length?: number) {
        this.writeBitStream(new BitStream(buffer), length * 8);
    }
}
