import PlayerRef from "../../player-ref";
import Game from "../game";

const { ccclass, property } = cc._decorator;

@ccclass
export default class SpecialSkill extends cc.Component {

    @property(cc.Node)
    uiRoot: cc.Node = null;

    @property({type: cc.SpriteFrame})
    skillFrames: cc.SpriteFrame[] = []

    @property({type: cc.SpriteFrame})
    normalFrames: cc.SpriteFrame[] = []

    @property({type: cc.SpriteFrame})
    bgFrames: cc.SpriteFrame[] = []

    private skill: cc.Button = null;
    private blockInput: cc.Node = null;

    private skillSprite: cc.Sprite = null;
    private bgSkillSprite: cc.Sprite = null;
    private normalSprite: cc.Sprite = null;
    private skillCooldown: cc.Label = null;

    start() {
        let fireBtn = this.uiRoot.getChildByName("button-fire");
        let skillBtn = this.uiRoot.getChildByName("button-skill");
        let firePad = this.uiRoot.getChildByName("fire-pad");
        let skillPad = this.uiRoot.getChildByName("skill-pad");
        
        this.skillSprite = (skillBtn.active ? skillBtn.children[1] : skillPad.children[2]).getComponent(cc.Sprite);
        this.bgSkillSprite = (skillBtn.active ? skillBtn.children[0] : skillPad.children[1]).getComponent(cc.Sprite);
        this.normalSprite = (fireBtn.active ? fireBtn.children[1] : firePad.children[1]).getComponent(cc.Sprite);
        this.skillCooldown = (skillBtn.active ? skillBtn.children[2] : skillPad.children[3]).getComponent(cc.Label);
        this.skill = skillBtn.active ? skillBtn.children[1].getComponent(cc.Button) : null;
        this.blockInput = skillPad.active ? skillPad.children[0] : null;
    }
    
    setPlayerSkill() {
        this.skillSprite.spriteFrame = this.skillFrames[PlayerRef.current.user];
        this.bgSkillSprite.spriteFrame = this.bgFrames[PlayerRef.current.user];
        this.normalSprite.spriteFrame = this.normalFrames[PlayerRef.current.bomb];
    }

    setData(skill: number, enable: boolean, cooldown?: any) {
        this.skill && (this.skill.interactable = enable);
        this.blockInput && (this.blockInput.active = !enable);
        this.skillSprite.spriteFrame = this.skillFrames[skill];
        this.skillSprite.node.color = enable ? cc.Color.WHITE : cc.Color.GRAY;
        this.skillCooldown.node.active = !enable;
        this.skillCooldown.string = cooldown;
    }
}