export namespace ConfigGame {
    export const FPS = 30;               // game server framerate
    export const TILE_SIZE = 64;           // tile size (px)
    export const WORLD_WIDTH = 28 * TILE_SIZE;     // world map width (px)
    export const WORLD_HEIGHT = 28 * TILE_SIZE;    // world map width (px)
    export const BOSS_COUNT = 10;        // total boss in world
    export const BOSS_CHANGEDIR_FRQ = 3;    // boss change dir frequency (seconds)
    export const BOSS_FIREBOMB_FRQ = 5;    // boss fire bomb frequency (seconds)
    export const PLAYER_WIDTH = 40 * 0.65;
    export const PLAYER_HALF_W = PLAYER_WIDTH / 2;
    export const PLAYER_HEIGHT = 45 * 0.65;
    export const PLAYER_HALF_H = PLAYER_HEIGHT / 2;
    export const PLAYER_SPEED = 200;     // px/second
    export const PLAYER_START_HEALTH = 70;
    export const PLAYER_EXP_GAIN_BASE = 50;
    export const PLAYER_LEVEL_MAX = 6;
    export const BOMB_SPEED = 480;      // px/second
    export const BOMB_RANGE = 80 * 0.65;       // explosion radius in px
    export const BOMB_LENGTH = 250 * 0.85;        // distance that bomb can throw to in px
    export const BOMB_BASE_DAMGE = 50;      // lvl 1 bomb damge
    export const EXP_BUBLE_WIDTH = 20 * 0.5;      // px
    export const EXP_BUBLE_HEIGHT = 20 * 0.5;      // px
    export const EXP_BUBLE_EXP = 5;      // px
    export const EXP_BUBLE_PER_SCREEN = 5;       // avergane bubles exper per screen
    export const EXP_BUBLE_COUNT = EXP_BUBLE_PER_SCREEN * Math.round((WORLD_WIDTH * WORLD_HEIGHT) / (960 * 640));   // 960x640 (design resolution)
    
    export const PLAYER_SKILL_COOLDOWN = 5; //@ seconds
    export const PLAYERS_AS_TARGET = 3;
    export const BURN_DAMAGE = 5;
    export const BURN_INTERVAL = 1;
    export const SLOW_PERCENT = 0.5;
                                // [None,   Immune, Dmg,    Def,    Exp,    Burn,   Slow,   Bind]
    export const EFFECT_DURATION = [0,      15,     10,     10,     10,     5,      5,      3]
    export const OBSTACLES = [
        [[2, 2, 3, 3]],
        [[8, 2, 6, 2]],
        [[18, 2, 6, 2], [23, 2, 3, 3]],
        [[9, 9, 9, 4]],
        [[1, 14, 5, 2]],
        [[22, 15, 5, 2]],
        [[12, 17, 5, 4]],
        [[2, 20, 3, 6], [4, 24, 6, 2]],
        [[18, 24, 6, 2], [23, 22, 3, 4]],
    ]


    // function utils
    export function getDamge(level: number): number {
        //level = level > PLAYER_LEVEL_MAX ? PLAYER_LEVEL_MAX : level; //@ remove lvl max
        let damge = BOMB_BASE_DAMGE;
        // for (let i = 2; i <= level; i++) {
        //     damge = damge + 5;
        // }
        return damge;
    }

    export function getExpGain(level: number): number {
        //level = level > PLAYER_LEVEL_MAX ? PLAYER_LEVEL_MAX : level; //@ remove lvl max
        let exp = PLAYER_EXP_GAIN_BASE;
        for (let i = 2; i <= level; i++) {
            exp = exp + PLAYER_EXP_GAIN_BASE / 2;
        }
        return exp;
    }

    export function getHealth(level: number): number {
        //level = level > PLAYER_LEVEL_MAX ? PLAYER_LEVEL_MAX : level; //@ remove lvl max
        let health = PLAYER_START_HEALTH * level;
        return health;
    }

    export function getLevelExp(level: number): number {
        //level = level > PLAYER_LEVEL_MAX ? PLAYER_LEVEL_MAX : level; //@ remove lvl max
        // let exp = 100;
        // for (let i = 2; i <= level; i++) {
        //     exp = exp + 100 / 2;
        // }
        // return exp;
        return 100 * Math.pow(2, level - 1);
    }
    
}