import EventManager from "../core/event-manager";



export enum State {
    NONE = 0,
    CACHING,
    CACHED
}


export default class Cacher {
    private static _instance: Cacher = null;
    static get instance() {
        if (this._instance == null) {
            this._instance = new Cacher();
        }
        return Cacher._instance;
    }

    mc_anims: {[key: number]: cc.AnimationClip[]} = {};
    map: {[key: string]: any} = {};
    
    load() {
        //@ mc animations
        // let dirs = ["rabbit", "penguin", "leaf"];
        // dirs.forEach((dir, i) => {
        //     cc.loader.loadResDir(
        //         dir, 
        //         cc.AnimationClip, 
        //         (err, clips) => {
        //             this.mc_anims[i] = [...clips];
        //         }
        //     )
        // })

        //@ tilemap
        cc.loader.loadRes("map/default", (err, tmxAsset) => {
            this.map["asset"] = tmxAsset;
            this.map["texture"] = {};
            let dirs = ["map/sand", "map/ice", "map/grass"];
            dirs.forEach((dir, i) => {
                cc.loader.loadRes(dir, (err, texture) => {
                    this.map["texture"][i] = texture;
                })
            })
            EventManager.instance.dispatch("Resources-Cached");
        })

        //@ loaded
        //if (Object.keys(this.mc_anims).length == this.mapTextures.length) {
        //    EventManager.instance.dispatch("Resources-Cached");
        //}

    }
}
