// Re-align widget when parent size change

const {ccclass, property} = cc._decorator;

@ccclass
export default class WidgetAlign extends cc.Component {

    private widget: cc.Widget;

    start () {
        this.node.parent.on(cc.Node.EventType.SIZE_CHANGED, this.onParentSizeChange, this);
        this.widget = this.getComponent(cc.Widget);
    }

    onParentSizeChange() {
        this.widget.updateAlignment();
    }
}
