
const {ccclass, property} = cc._decorator;


@ccclass
export default class Notifier extends cc.Component {
    @property(cc.Prefab)
    notifyItem: cc.Prefab = null;

    private container: cc.Layout = null;

    start() {
        this.container = this.getComponent(cc.Layout);
    }

    addNotify(message: string) {
        let noti = cc.instantiate(this.notifyItem);
        noti.getComponent(cc.RichText).string = message;
        this.node.insertChild(noti, 0);
        noti.runAction(cc.sequence(
            cc.delayTime(3),
            cc.fadeOut(0.5),
            cc.callFunc(() => {
                noti.removeFromParent();
            })
        ))
    }
}