import PlayerRef from "../../player-ref";
import SequenceManager from "./SequenceManager"
import { SequenceNode } from "./SequenceNode";

const {ccclass, property} = cc._decorator;

@ccclass
export default class squence_first_launch extends cc.Component
{
    onLoad ()
    {
        var sm = this.getComponent(SequenceManager);
        var prev = false;
        if(PlayerRef.current.first_launch_step < 1)
        {
            sm.push(this.node.children[0].getComponent(SequenceNode));
            prev = true;
        }
        // if(PlayerRef.current.first_launch_step < 2)
        // {
        //     if(prev)    sm.push(this.node.children[1].getComponent(SequenceNode));
        //     sm.push(this.node.children[2].getComponent(SequenceNode));
        //     prev = true;
        // }console.log("squence_first_launch - onLoad - " + PlayerRef.current.first_launch_step);
    }
}
