
// random unique
export class RandomUnique {

    private unique: number[] = [];
    private initLength = -1;
    private recycle: boolean = false;

    constructor(length: number, recycle: boolean = false) {
        this.initLength = length;
        this.recycle = recycle;
        for(let i = 0; i < length; i++) {
            this.unique[i] = i;
        }
    }

    random(): number {
        if(this.unique.length == 0) {
            if(this.recycle && this.initLength != -1) {
                for(let i = 0; i < this.initLength; i++) {
                    this.unique[i] = i;
                }
            } else {
                return -1;
            }            
        } 
        let idx = Math.floor(Math.random() * (this.unique.length - 1));
        let ret = this.unique[idx];
        this.unique.splice(idx, 1);
        return ret;
    }
}

export class Constant {
    static MAX_SAFE_INTEGER = 9007199254740991;
    static MIN_SAFE_INTEGER = -9007199254740991;
}

export class Utils{
	static ENCODE_LOCAL_STORAGE: boolean = true;
	
    public static getCurrentSceneName(): string {
        let scene: string = "";
        (cc.game as any)._sceneInfos.forEach(element => {
            if(element.uuid == (cc.director as any)._scene._id) {
                scene = element.url;
                scene = scene.substring(scene.lastIndexOf('/')+1).match(/[^\.]+/)[0]
            }
        });
    
        return scene;
    }

    // lerp from 'from' to 'to' number with ratio
    public static lerp(from: number, to: number, ratio: number) {
        ratio = ratio < 0 ? 0 : ratio;
        ratio = ratio > 1 ? 1 : ratio;
        return from + (to - from) * ratio;
    }

    public static genUId(): string {
        return (Date.now().toString(36) + Math.random().toString(36).substr(2, 5));
    }

    public static extend(obj, src) {
        if(src == null) return obj;
        if(obj == null) obj = {};
        Object.keys(src).forEach(function(key) { obj[key] = src[key]; });
        return obj;
    }

    public static randomRange(min: number, max: number) {
        return Math.random() * (max - min) + min;
    }

    public static zeropad(n: number, d: number): string {
		var s = (n < 0 ? "-" : "");
		if (n < 0) n = -n;
		var zeroes = d - n.toString().length;
		for (var i = 0; i < zeroes; i++)
			s += "0";
        return (s + n.toString());
    }

    public static sign(value: number) {
        return value > 0 ? 1 : value < 0 ? -1 : 0;
    }

    public static moveTowards(current: number, target: number, maxDelta: number): number {
        if(Math.abs(target - current) <= maxDelta) {
            return target;
        }
        return current + Utils.sign(target - current) * maxDelta;
    }

    public static v2MoveTowards(current: cc.Vec2, target: cc.Vec2, maxDelta: number): cc.Vec2 {
        let diff = target.sub(current);
        let mag = diff.mag();
        if(mag <=  maxDelta || mag == 0) {
            return target;
        }
        return current.add(diff.div(mag).mul(maxDelta))
    }
	
	/**
     * add padding number - exp: (n = 1, d = 2) => 1 = 01
     * @param n 
     * @param d 
     */
    public static zeropad(n: number, d: number): string 
    {
		var s = (n < 0 ? "-" : "");
		if (n < 0) n = -n;
		var zeroes = d - n.toString().length;
		for (var i = 0; i < zeroes; i++)
			s += "0";
        return (s + n.toString());
    }
	
	public static Atob (a: any): any 
    {
        if (a != null && this.ENCODE_LOCAL_STORAGE) 
        {
            return atob(a);
        }
        return a;
    }
	
	public static Btoa (a: any): any {
        if (a != null && this.ENCODE_LOCAL_STORAGE) 
        {
            return btoa(a);
        }
        return a;
    }   
}