if(!window.i18n) window.i18n = {};
if(!window.i18n.languages) window.i18n.languages = {};


const Polyglot = require('polyglot');
const supported_languages = window.i18n.supported_languages || [];

let saveLanguage = CC_EDITOR ? null : cc.sys.localStorage.getItem("__gamesave-language");
let lang = '';
if(saveLanguage == null) {      // first time, load default from system lang
    lang = cc.sys.language;
    if(supported_languages.length > 0 && supported_languages.indexOf(lang) == -1) {   // system lang not support -> choose the 1st
        lang = supported_languages[0];
        // save selected
        if(!CC_EDITOR) {
            cc.sys.localStorage.setItem("__gamesave-language", lang);
        }
    }
} else {    // we have the save lang
    lang = saveLanguage;
}


let data = window.i18n.languages[lang] || {}; // update this to set your default displaying language in editor
// let polyglot = null;
let polyglot = new Polyglot({phrases: data, allowMissing: true});


module.exports = {
    /**
     * This method allow you to switch language during runtime, language argument should be the same as your data file name
     * such as when language is 'zh', it will load your 'zh.js' data source.
     * @method init
     * @param language - the language specific data file name, such as 'zh' to load 'zh.js'
     */
    init (language) {
        if(!window.i18n.languages[language]) {
            console.warn('Language is not support: ' + language);
            return false;
        }
        if(!CC_EDITOR && window.i18n.languages[language]) {    // save lang
            cc.sys.localStorage.setItem("__gamesave-language", language);
        }
        lang = language;
        data = window.i18n.languages[lang] || {};
        polyglot.replace(data);
        return true;
    },
    /**
     * return the save language
     */
    getSaveLang () {
        return cc.sys.localStorage.getItem("__gamesave-language");
    },
    /**
     * this method takes a text key as input, and return the localized string
     * Please read https://github.com/airbnb/polyglot.js for details
     * @method t
     * @return {String} localized string
     * @example
     *
     * var myText = i18n.t('MY_TEXT_KEY');
     *
     * // if your data source is defined as
     * // {"hello_name": "Hello, %{name}"}
     * // you can use the following to interpolate the text
     * var greetingText = i18n.t('hello_name', {name: 'nantas'}); // Hello, nantas
     */
    t (key, opt) {
        return polyglot.t(key, opt);
    }
};