import { SequenceNodeShowSelectSkinPopup } from './SequenceNodeShowSelectSkinPopup'
import { SequenceNodeShowPopup } from './SequenceNodeShowPopup';
import PlayerRef from "../../player-ref";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SequenceNodeShowGreetingPopup extends SequenceNodeShowSelectSkinPopup
{
    public ShowPopup()
    {//console.log("SequenceNodeShowServerSelectionPopup - ShowPopup");
        this.main_menu.showGreetingPopup();
        this.main_menu.selectSkinPopup_setTitle("WELCOME");
    }

    public OnCloseButtonPressed(event, customEventData)
    {console.log("SequenceNodeShowGreetingPopup - OnCloseButtonPressed");
        super.OnCloseButtonPressed(event, customEventData);
        PlayerRef.current.first_launch_step = 1;
    }
}
