import Loader from "../core/loader";
import Player from "./player";
import Bomb from "./bomb";
import Client, { ClientListener } from "./multiplayer/client";
import { GameInfo, GameInitInfo, GameData, LbInfo, NotifyInfo, DeathInfo } from "./multiplayer/shared/IMessageData";
import PlayerRef, { DesignType } from "../player-ref";
import CameraControl from "./camera-control";
import PlayerControl from "./player-control";
import Leaderboard from "./ui/leaderboard";
import EventManager from "../core/event-manager"
import Notifier from "./notifier";
import MiniMap from "./ui/minimap";
import ExpBuble from "./exp-buble";
import PlayerControlLocal from "./player-control-local";
import NetComponent from "./net-component";
import Popup from "../core/popup";
import SpecialSkill from "./ui/special-skill";
import Cacher from "./resources-cacher";
import SoundManager from "../core/sound-manager";
/**
 * Client - Server connect step
 * - C - Open connection to server. (if success then)
 * - C - Request game info. (send: 'gameinfo' msg to server)
 * - S - Return game info. content: <world_w>|<world_h>|[other infos]
 * - C - Recive game info to init world.
 * - C - Request join. (send: 'join' msg with data: <player_name>)
 * - S - Accept join. Send back: <player id>|<start pos>|<start health>#<all current player infos: id|name|pos>
 * - C - Init players. Subscribe game update msg channel 'serverdata'
 * - S - Send game info at 30fps. <player id>|<pos><dir>|<player action>;<player id>|<pos><dir>|<player action>#<bom id>|<pos>|[explore]
 * - C - Send local action. <player action>
 * 
 * - S - Send death - end
 * - C - Request player name. Send 'queryplayer' msg with data <player id> - S - return with player name or null if not exist
 */

const { ccclass, property } = cc._decorator;

@ccclass
export default class Game extends cc.Component implements ClientListener {

    static instance: Game = null;
    static isTutorial: boolean = true;

    // lhchi add
	static USE_NEW_PORTAL: boolean = true;
    static USE_DYNAMIC_BOMB: boolean = false; // use for Dynamic range of bomb
    static pressed_btnfire: boolean = false;
    static dynamic_range = 20;
    static min_range = 20;
    static max_range = 100;
    static count = 3;

    @property(cc.Node)
    worldRoot: cc.Node = null;

    @property(cc.Node)
    map: cc.Node = null;

    @property(cc.Node)
    killerPopup: cc.Node = null;

    @property(Notifier)
    notifier: Notifier = null;

    @property(Leaderboard)
    leaderboard: Leaderboard = null;

    @property(MiniMap)
    miniMap: MiniMap = null;

    @property({type: cc.Prefab})
    playerPrefabs: cc.Prefab[] = [];

    @property(cc.Prefab)
    bombPrefab: cc.Prefab = null;

    @property(cc.Prefab)
    expBublePrefab: cc.Prefab = null;

    @property(cc.Prefab)
    targetPrefab: cc.Prefab = null;

    @property({type: cc.SpriteFrame})
    mcFrames: cc.SpriteFrame[] = [];

    localPlayer: Player = null;
    localTarget: cc.Node = null;
    players: { [key: number]: Player } = {};
    bombs: { [key: number]: Bomb } = {};
    bubles: { [key: number]: ExpBuble } = {};
    
    client: Client = null;
    private isDeath: boolean = false;


    // game info
    worldSize: cc.Vec2 = null;
    playerSize: cc.Vec2 = null;
    bombRange: number = 0;
    playerSpeed: number = 0;
    skill: SpecialSkill = null;

    onLoad() {
        if (!Loader.loaded) return;      // prevent run when loader init

        Popup.instance.setTarget(this.node.getChildByName("uiRoot")); //set target
        Popup.instance.showWaitingPopup("connecting to server...");
        Game.instance = this;
        this.client = new Client(this);
        this.client.connect();

        //load map
        let tilemap = this.map.addComponent(cc.TiledMap);
        tilemap.tmxAsset = Cacher.instance.map["asset"];
        tilemap.tmxAsset.textures[0] = Cacher.instance.map["texture"][PlayerRef.current.map];
    }

    start() {
        if (!Loader.loaded) return;      // prevent run when loader init
        EventManager.instance.register(NetComponent.EVENT_NET_ENTITY_DEATH, this.onNetEntityDeath, this);
        SoundManager.instance.playMusic("m_ih_bgm");
        this.map.active = true;

        if(Game.USE_DYNAMIC_BOMB == true)
        {
            ((this.node.getChildByName("uiRoot")).getChildByName("fire-pad")).getChildByName("bg").active = true;
        }
        else
        {
            ((this.node.getChildByName("uiRoot")).getChildByName("fire-pad")).getChildByName("bg").active = false;
        }
    }

    onNetEntityDeath(id: string) {
        if (this.players[id]) {
            delete this.players[id];
            this.miniMap.removeFromMap([id]);
        } else if (this.bombs[id]) {
            delete this.bombs[id];
        } else if (this.bubles[id]) {
            delete this.bubles[id];
        }
    }

    // clientlistener implement
    onGameInfo(info: GameInfo) {//console.log("hoang.tuduc - game - onGameInfo");
        cc.log("game info: " + info.worldW + " - " + info.worldH);
        this.worldRoot.width = info.worldW;
        this.worldRoot.height = info.worldH;

        this.worldSize = cc.v2(info.worldW, info.worldH);
        this.playerSize = cc.v2(info.playerW, info.playerH);
        this.bombRange = info.bombRange;
        this.playerSpeed = info.playerSpeed;

        // join game
        this.client.joinGame({name: PlayerRef.current.name, cskin: PlayerRef.current.user, bskin: PlayerRef.current.bomb});
    }

    onJoinGame(info: GameInitInfo) {
        // lb data
        //this.schedule(this.updateLeaderBoard.bind(this), 0.5);
        // create my player
        let myPlayer = cc.instantiate(this.playerPrefabs[PlayerRef.current.user]).getComponent(Player);
        myPlayer.init({ ...info, level: 1, exp: 0, health: info.startHealth, name: PlayerRef.current.name, cskin: PlayerRef.current.user});
        myPlayer.node.addComponent(PlayerControlLocal).init(this.client);
        this.worldRoot.addChild(myPlayer.node);
        this.localPlayer = myPlayer;
        CameraControl.instance.setTarget(myPlayer.node);
        
        this.skill = this.getComponent(SpecialSkill);
        this.skill.setPlayerSkill();
        
        if (!this.localTarget) {
            this.localTarget = cc.instantiate(this.targetPrefab);
            this.localTarget.active = false;
            this.worldRoot.addChild(this.localTarget);
        }
        
        //dismis waiting popup
        Popup.instance.hideWaitingPopup();
    }

    onServerData(data: GameData) {
        data.players.forEach(pinfo => {
            if (this.localPlayer.id == pinfo.id) {  // process for local player
                this.localPlayer.controller.setServerData(pinfo);
                this.skill.setData(pinfo.cskin, pinfo.skillable, pinfo.cooldown);
            } else if (this.players[pinfo.id]) {
                this.players[pinfo.id].controller.setServerData(pinfo);
            } else {    // new player join
                let player = cc.instantiate(this.playerPrefabs[pinfo.cskin]).getComponent(Player);
                player.init({ ...pinfo, name: "" });
                player.node.addComponent(PlayerControl);
                this.worldRoot.addChild(player.node);
                this.players[player.id] = player;
                this.client.getPlayerExtraInfo(player.id, data => {
                    if (data && data.id == player.id) {
                        player.setPlayerName(data.name);
                    }
                })
            }
        })

        data.bombs.forEach(binfo => {
            if (this.bombs[binfo.id]) {
                this.bombs[binfo.id].setStateData(binfo);
            } else {
                let bomb = cc.instantiate(this.bombPrefab).getComponent(Bomb);
                bomb.init(binfo);
                this.worldRoot.addChild(bomb.node);
                this.bombs[binfo.id] = bomb;
                this.client.getBombExtraInfo(bomb.id, data => {
                    if (data && data.id == bomb.id) {
                        bomb.setExtraData(data);
                    }
                })
            }
        })

        data.bubles.forEach(bubleInfo => {
            if (this.bubles[bubleInfo.id]) {
                this.bubles[bubleInfo.id].setStateData(bubleInfo);
            } else {
                let buble = cc.instantiate(this.expBublePrefab).getComponent(ExpBuble);
                buble.init(bubleInfo);
               // cc.log(buble);
                //buble.node.color = cc.Color.RED;
                this.worldRoot.addChild(buble.node);
                this.bubles[bubleInfo.id] = buble;
            }
        })
        
        //@ update minimap
        this.miniMap.updatePlayersPosition(this.players);
    }

    updateLeaderBoard() {
        if (!this.localPlayer) return;

        this.client.getLeaderBardData(this.localPlayer.id, (data) => {
            this.leaderboard.setData(data);
            this.miniMap.updatePlayersDisplay(data);
            this.localPlayer.controller.updateRank(data, true);
            for (let id in this.players) {
                this.players[id].controller.updateRank(data);
            }
        })
    }

    onLeaderboardChange(data: LbInfo[]) {
        this.leaderboard.setData(data);
        this.miniMap.updatePlayersDisplay(data);
        this.localPlayer.controller.updateRank(data, true);
        for (let id in this.players) {
            this.players[id].controller.updateRank(data);
        }
    }

    updateLocalTarget(direction: number, range: number) {
        if (direction > -1 && range > 0) {
            let len = range * this.bombRange / 100;
            let rad = direction * Math.PI / 180;
            let x = this.localPlayer.node.x + Math.round(len * Math.cos(rad));
            let y = this.localPlayer.node.y + this.playerSize.y + Math.round(len * Math.sin(rad));
            this.localTarget.setPosition(x, y);
            this.localTarget.active = true;
        } else {
            this.localTarget.active = false;
        }
    }

    onDeath(data: DeathInfo) {
        PlayerRef.current.updateLocalRecord(data.score, data.time);
        //this.unschedule(this.updateLeaderBoard.bind(this));
        
        this.isDeath = true;
        this.killerPopup.active = true;
        let timePlay = "" + Math.floor(data.time / 60) + "\' " + Math.floor(data.time % 60) + "\"";
        this.killerPopup.getComponentInChildren(cc.Label).string = "Killed by " + data.killer;
        
        // * Bug Fixed ['10592233']: '[HTML5][AP] : AUT will take more time Loading time to re-enter the AP after exiting through ECS.
        // this.client.disconnect();

        this.killerPopup.runAction(cc.sequence(
            cc.fadeIn(0.5),
            cc.delayTime(1),
            cc.fadeOut(0.25),
            cc.callFunc(() => {
                // this.client.shutdown();
                this.killerPopup.active = false;
                SoundManager.instance.stop("m_ih_bgm");
                // show ADS
                Game.instance.showAds();
            }),
        ));
		
        this.scheduleOnce(function() {   
            Popup.instance.showConfirmPopup(
                this.mcFrames[PlayerRef.current.user],
                timePlay, //time-played
                data.score.toString(), //score
                this.replay.bind(this),
                this.quit.bind(this)
            );        
       }, 3);
    }

    replay() {
        if (window["dataLayer"]) {
            window["dataLayer"].push({
                'event': 'ga_event',
                'ga_category': 'Gamepage',
                'ga_action': 'Start',
                'ga_label': window["productTitle"],
                'ga_noninteraction': false
            });
        }
        cc.director.loadScene('game');
    }

    quit() {
        if (window["dataLayer"]) {
            window["dataLayer"].push({
                'event': 'ga_event',
                'ga_category': 'Gamepage',
                'ga_action': 'Completion',
                'ga_label': window["productTitle"],
                'ga_noninteraction': false
            });
        }
        this.client.disconnect();
        cc.director.loadScene('main-menu');
    }

    onNotiMessage(msg: NotifyInfo) {
        if (msg.kill) {
            SoundManager.instance.playRandom(["vo_jelly_die_1", "vo_jelly_die_2", "vo_jelly_die_3", "vo_jelly_die_4"]);
            this.notifier.addNotify("<color=#00ff00>" + msg.kill.killer + "</c> killed <color=#ff0000>" + msg.kill.deathman + "</c>");
        }
    }

    onError(error) {
        // console.log("game - onError - " + this.isDeath);
        //this.unschedule(this.updateLeaderBoard.bind(this));
        if(this.isDeath) return;

        cc.error(error);//console.log("game - onError - " + error);
        this.client.shutdown();
        Popup.instance.showInfoPopup(
            "Opss, something's wrong...\nPlease try again later!", 
            () => { 
                cc.director.loadScene('main-menu') 
            }
        )
        //cc.director.loadScene('main-menu');
    }

    onDestroy() {
        EventManager.instance.unregisterTarget(this);
        delete this.client;
    }

    onButtonQuit(event) {
        SoundManager.instance.stop("m_ih_bgm");
        SoundManager.instance.playEffect("sfx_ui_back");
        Popup.instance.showInfoPopup(
            null, 
            this.quit.bind(this), 
            true, 
            () => { SoundManager.instance.playMusic("m_ih_bgm");}
        );
    }

    showAds()
    {
        console.log("showAds()");
        if (window["enableADS"])
        {
            console.log("showing ADS");
            try {
                window["playAds"]();
            }
            catch(error) {
                console.error(error);
            }
        }
    }
}
