import Game from "../game";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Debug extends cc.Component {

    @property({type: Game})
    game: Game = null;

    @property({type: cc.Label})
    debugInfoText: cc.Label = null;

    @property()
    showCollidBox = false;

    private debugDrawer: cc.Graphics = null;
    private drawBombRange = true;
    private debugInfo: {} = {};
    private joinGame = false;

    update(dt: number) {
        if(this.joinGame == false && this.game.localPlayer) {
            this._onJoinGame();
        }

    }

    lateUpdate() {
        // draw collider
        this.drawCollder();
    }

    _onJoinGame() {
        this.joinGame = true;
        this.debugPing();
    }

    debugPing() {
        this.game.client && this.schedule(() => {
            this.game.client.ping(time => {
                this.setInfo("ping", "" + time + "ms");
            })
        }, 3);
    }

    setInfo(key: string, value: string) {
        this.debugInfo[key] = value;
        this.updateDebugInfo();
    }

    updateDebugInfo() {
        let debugText = "";
        for(let key in this.debugInfo) {
            debugText += key + " : " + this.debugInfo[key] + '\n';
        }
        this.debugInfoText.string = debugText;
    }

    drawCollder() {
        if (!this.showCollidBox) return;

        if (this.debugDrawer == null) {
            let node = cc.find("Debug_Drawer");
            if (node == null) {
                node = new cc.Node("Debug_Drawer");
                node.zIndex = cc.macro.MAX_ZINDEX;
                cc.game.addPersistRootNode(node);
            }
            this.debugDrawer = node.addComponent(cc.Graphics);
        }

        this.debugDrawer.clear();
        for (let id in this.game.players) {
            this._drawNode(this.game.players[id].node, this.game.playerSize.x / 2, this.game.playerSize.y / 2);
        }
        this.game.localPlayer && this._drawNode(this.game.localPlayer.node, this.game.playerSize.x / 2, this.game.playerSize.y / 2);

        if (this.drawBombRange) {
            let w2 = this.game.bombRange * 0.5;
            let h2 = this.game.bombRange * 0.8660254037844386;
            for (let id in this.game.bombs) {
                if (this.game.bombs[id].exploded) {
                    this._drawNode(this.game.bombs[id].node, w2, h2);
                    this._drawNode(this.game.bombs[id].node, h2, w2);
                }
            }
        }

    }

    _drawNode(node: cc.Node, w2: number, h2: number) {
        this.debugDrawer.strokeColor = cc.Color.WHITE;
        let world = node.convertToWorldSpaceAR(cc.Vec2.ZERO);
        this.debugDrawer.moveTo(world.x - w2, world.y + h2);
        this.debugDrawer.lineTo(world.x + w2, world.y + h2);
        this.debugDrawer.lineTo(world.x + w2, world.y - h2);
        this.debugDrawer.lineTo(world.x - w2, world.y - h2);
        this.debugDrawer.close();
        this.debugDrawer.stroke();
    }
}