const {ccclass, property, requireComponent, executeInEditMode, menu} = cc._decorator;

const i18n = require('i18n');

@ccclass
@requireComponent(cc.Label)
@executeInEditMode
@menu('i18n:MAIN_MENU.component.renderers/LabelLocalized')
export default class LabelLocalized extends cc.Component {

    @property()
    _textKey: string = "TEXT_KEY";

    @property({
        multiline: true,
        tooltip: "Enter i18n text key"
    })
    set textKey (value) {
        this._textKey = value;
        this.updateLabel();
    }
    get textKey() { return this._textKey; }

    protected label: cc.Component = null;
    protected textProp = null;

    onLoad() {
        this.label = this.getComponent(cc.Label);
        this.updateLabel();
    }

    updateLabel () {
        if(this.label == null)
            this.label = this.getComponent(cc.Label);
        if(this.label)
        {
            let keys = this.textKey.trim().split("+");
            let text = '';
            for(let i = 0; i < keys.length; ++i)
            {
                text += i18n.t(keys[i], this.textProp);
            }
            (this.label as any).string = text;
        }
    }

    updateTextProp(props: {}) {
        this.textProp = props;
        this.updateLabel();
    }
}

