import { GameInfo, GameInitInfo, GameData, PlayerInfo, BombInfo, LbInfo, PlayerExtraInfo, BombExtraInfo, NotifyInfo, DeathInfo, PlayerInput } from "./shared/IMessageData";
import { SCCustomCodec } from "./shared/SCCustomCodec";
import { Message, MsgGameInfo, MsgInputAction, MsgJoinGame, MsgQueryPlayer, MsgQueryBomb, MsgLeaderBoard, MsgServerData, MsgNofity, MsgDeath } from "./shared/NetMessage";
import SoundManager from "../../core/sound-manager";
import MainMenu from "../menu/main-menu";
import Player from "../player";

import PlayerRef from "../../player-ref";//hoang.tuduc - get last server user select

// game to socket cluster implement

export interface ServerDesc {
    name: string,
    host: string,
    port: number,
    secure: boolean
}

export interface ClientListener {
    onGameInfo(info: GameInfo);
    onJoinGame(info: GameInitInfo);
    onServerData(data: GameData);
    onError(error);
    onDeath(data: DeathInfo);
    onNotiMessage(msg: NotifyInfo);
    onLeaderboardChange(data: LbInfo[]);
}

// let localhost = {name: "192.168.1.8", host: "113.173.97.155", port: 8000, secure: false };
//let localhost = {name: "192.168.1.8", host: "192.168.1.8", port: 8000, secure: false };

export default class Client {
    private currentTick = 0;        // number of tick receive from server, use to debug package loss

    private static ms_socket: SCClientSocket = null;//hoang.tuduc - only socket

    private socket: SCClientSocket = null;
    private channel: SCChannel = null;
    private game: ClientListener = null;

    constructor(game: ClientListener) {
        this.game = game;
    }

    connect() {
        // console.log("Client - connect - : " + this.socket);
        if(this.socket) return;

        var server = MainMenu.servers[PlayerRef.current.last_server_selection];
        //server = localhost;
        this.socket = socketCluster.create({
            // //hostname: '127.0.0.1',
            //hostname: '10.219.12.186',
            // local
            // hostname: '10.219.8.191',
            // port: 8000,
            // private server
            //hostname: '35.201.210.174',
            //port: 443,
            // html5 server
            
            // hostname: '13.233.33.68',
            hostname:server.host,//'wss.ludigames.com',
            port: server.port,//3001,
            secure: server.secure,//true,
            rejectUnauthorized: false,

            autoReconnect: false,
            codecEngine: new SCCustomCodec(),
        })
        
        this.socket.on('connect', () => {//hoang.tuduc - server is connected at select server
            // cc.log("Socket connected");

            this.socket.emit(Message.GameInfo, null, (error, data) => {
                if (error == null) {// console.log("Client - connect - msg");
                    this.game.onGameInfo(new MsgGameInfo().unpack(data));
                } else {
                    this.game.onError("Get game info error: " + error);
                }
            })
        })
        this.socket.on('error', (error) => {
            
            cc.warn("error: " + error.message);
            this.game.onError(error);
        })
        this.socket.on('disconnect', () => {
            cc.warn("disconnect " + this.socket.getState());
            this.shutdown();
        })
        this.socket.on(Message.Death, (data) => {
            this.game.onDeath(new MsgDeath().unpack(data));
        })
    }

    connectToHost(desc: ServerDesc, onConnected: () => void, onFailed: (e: Error) => void = null, force: boolean = false)
    {        
        if(!force && this.socket)
        {
            onConnected();//hoang.tuduc - server connected so we mock callback onConnected
            return;
        }

        this.socket = Client.ms_socket;

        if(this.socket) this.disconnect();//// console.log("Client - connectToHost - force: " + force);

        if(force || !this.socket)
        {
            // console.log("Client - connectToHost - host: " + desc.host);
            this.socket = socketCluster.create({
                // //hostname: '127.0.0.1',
                //hostname: '10.219.12.186',
                // local
                // hostname: '10.219.8.191',
                // port: 8000,
                // private server
                //hostname: '35.201.210.174',
                //port: 443,
                // html5 server
                
                // hostname: '13.233.33.68',
                // hostname:'wss.ludigames.com',
                hostname: desc.host,//'jellimon-be.ludigames.com',//
                port: desc.port,//3001,//
                secure: desc.secure,//true,//
                rejectUnauthorized: false,

                autoReconnect: false,
                codecEngine: new SCCustomCodec(),
            })

            Client.ms_socket = this.socket;//hoang.tuduc - hold socket
        }

        this.socket.on('connect', () => {
            // cc.log("Socket connected");
            onConnected();
        })        
        this.socket.on('error', (error) => { // console.log()
            // cc.warn("error " + this.socket.getState());
            if(onFailed)    onFailed(error);
        })
        this.socket.on('disconnect', () => {
            // cc.warn("disconnect " + this.socket.getState());
            this.shutdown();
        })
    }

    shutdown() {// console.log("Client - shutdown");
        if (!this.socket || this.socket.getState() != "closed") return;

        this.socket.destroy();
        this.socket = null;
    }

    disconnect() { if (!this.socket || this.socket.getState() != "open") return;
        // console.log("Client - disconnect - " + (!this.socket || this.socket.getState() != "open"));
        this.socket.disconnect();
    }

    sendAction(action: PlayerInput) { if (!this.socket) return;
        // console.log("Client - sendAction - " + this.socket.id + " - " + action.direction);
        this.socket.emit(Message.InputAction, new MsgInputAction().pack(action));
    }

    //hoang.tuduc - impl send request
    sendRequest(event: string, callback: (error: Error, res: any) => void) {
        this.socket.emit(event, null, callback);
    }

    joinGame(player: {name, cskin, bskin}) {// console.log("Client - joinGame");
        if (!this.socket) return;
        
        this.socket.emit(Message.JoinGame, player, (error, data) => {
            if (error == null) {
                this.game.onJoinGame(new MsgJoinGame().unpack(data));
                this.registerServerData();
            } else
                this.game.onError(error);
        })
    }

    getPlayerExtraInfo(id: number, callback: (info: PlayerExtraInfo) => void) {// console.log("Client - joinGame");
        if (!this.socket) return;

        this.socket.emit(Message.QueryPlayer, id, (error, data) => {
            if (error == null) {
                callback(new MsgQueryPlayer().unpack(data));
            }
            else
                this.game.onError(error);
        })
    }

    getBombExtraInfo(id: number, callback: (info: BombExtraInfo) => void) {// console.log("Client - getBombExtraInfo");
        if (!this.socket) return;

        this.socket.emit(Message.QueryBomb, id, (error, data) => {
            if (error == null) {
                callback(new MsgQueryBomb().unpack(data));
            }
            else
                this.game.onError(error);
        })
    }

    getLeaderBardData(id: number, callback: (lbdata: LbInfo[]) => void) {// console.log("Client - getLeaderBardData");
        if (!this.socket) return;

        this.socket.emit(Message.LeaderBoard, id, (error, data) => {
            if (error == null)
                callback(new MsgLeaderBoard().unpack(data));
            else
                this.game.onError(error);
        })
    }

    registerServerData() {// console.log("Client - registerServerData");
        if (!this.socket) return;

        this.socket.on(Message.ServerData, (data) => {
            this.game.onServerData(new MsgServerData().unpack(data));
        })

        this.socket.subscribe(Message.Notify).watch((data) => {
            this.game.onNotiMessage(new MsgNofity().unpack(data));
        })

        this.socket.subscribe("lbdata").watch((data) => {
            this.game.onLeaderboardChange(new MsgLeaderBoard().unpack(data));
        })

        this.socket.subscribe("fps-dropped").watch((data) => {
            cc.log("[fps-dropped]" + data);
        })

        this.socket.on("server-sfx-trigger", (data) => {
            SoundManager.instance.playEffect(data);
        });
    }

    ping(callback: (pingTime: number) => void) {// console.log("Client - ping");
        if (!this.socket) return;

        let startTime = Date.now();
        this.socket.emit('ping', null, () => {
            let time = Date.now() - startTime;
            callback(time);
        })
    }
}
