const { ccclass, property } = cc._decorator;

import EventManager from './event-manager'
import { RandomUnique } from '../utils';

@ccclass
export default class SoundManager {

    private sounds: { [key: string]: cc.AudioClip } = {};
    private playingSounds: { [key: string]: number } = {}

    private static _instance: SoundManager = null;

    private initialized: boolean = false;
    private enable: boolean = true;
    private effectEnable: boolean = true;
    private musicEnable: boolean = true;
    private DEBUG_LOG = false;

    static get instance() {
        if (SoundManager._instance == null) {
            SoundManager._instance = new SoundManager();
        }
        return SoundManager._instance;
    }

    public get soundEnable() { return this.enable; }
    setEnable(enable: boolean) {
        this.enable = enable;
        cc.sys.localStorage.setItem("Sound_enable", enable ? 1 : 0);
        this.setEffectEnable(enable);
        this.setMusicEnable(enable);
    }

    public get soundEffectEnable() { return this.effectEnable; }
    setEffectEnable(enable: boolean) {
        this.effectEnable = enable;
        cc.sys.localStorage.setItem("Sound_effect_enable", enable ? 1 : 0);
    }

    public get soundMusicEnable() { return this.musicEnable; }
    setMusicEnable(enable: boolean) {
        this.musicEnable = enable;
        cc.sys.localStorage.setItem("Sound_music_enable", enable ? 1 : 0);
    }

    log(message) {
        if (this.DEBUG_LOG) {
            cc.log(message);
        }
    }

    error(message) {
        if (this.DEBUG_LOG) {
            cc.error(message);
        }
    }

    init() {
        // load from resource
        cc.loader.loadResDir('sounds', cc.AudioClip, (error, assets: cc.AudioClip[], urls: string[]) => {
            if(urls.length > 0) {
                assets.forEach((sound, index) => {
                    this.sounds[sound.name] = sound;
                });
            }
            this.initialized = true;
            this.enable = cc.sys.localStorage.getItem("Sound_enable") ? parseInt(cc.sys.localStorage.getItem("Sound_enable")) == 1 : true;
            this.effectEnable = cc.sys.localStorage.getItem("Sound_effect_enable") ? parseInt(cc.sys.localStorage.getItem("Sound_effect_enable")) == 1 : true;
            this.musicEnable = cc.sys.localStorage.getItem("Sound_music_enable") ? parseInt(cc.sys.localStorage.getItem("Sound_music_enable")) == 1 : true;
            cc.log("sound init");
            EventManager.instance.dispatch("Sound_initialized");
        });

    }

    playMusic(clipName: string, loop: boolean = true) {
        if (!this.musicEnable || !this.initialized) return null;

        clipName = clipName.toLowerCase();
        if (this.sounds[clipName]) {
            this.log('play music: ' + clipName);
            let id = cc.audioEngine.playMusic(this.sounds[clipName], loop);
            this.playingSounds[clipName] = id;
            return id;
        }
        else {
            this.error("playMusic - clipName: " + clipName + " not found");
        }
        return null;
    }

    playEffect(clipName: string, loop: boolean = false) {
        if (!this.enable || !this.initialized) return null;

        clipName = clipName.toLowerCase();
        if (this.sounds[clipName]) {
            this.log('play effect: ' + clipName);
            let id = cc.audioEngine.playEffect(this.sounds[clipName], loop);
            this.playingSounds[clipName] = id;
            return id;
        }
        else {
            this.error("playEffect - clipName: " + clipName + " not found");
        }
        return null;
    }

    playRandom(clipsName: string[], loop: boolean = false, effect: boolean = true) {
        if (!this.enable || !this.initialized) return null;

        let idx = Math.floor(Math.random() * clipsName.length);
        let clipName = clipsName[idx].toLowerCase();
        if (this.sounds[clipName]) {
            this.log('play random: ' + clipName);
            let id = null;
            if (effect) {
                id = this.playEffect(clipName, loop);
            } else {
                id = this.playMusic(clipName, loop);
            }
            this.playingSounds[clipName] = id;
            return id;
        }
        else {
            this.error("playRandom - clipName: " + clipsName[idx] + " not found");
        }
        return null;
    }


    pause(clipName: string) {
        if (!this.enable) return;

        clipName = clipName.toLowerCase();
        if (this.playingSounds[clipName] != undefined && cc.audioEngine.getState(this.playingSounds[clipName]) === cc.audioEngine.AudioState.PLAYING) {
            cc.audioEngine.pause(this.playingSounds[clipName]);
        } else {
            this.error("Pause sound: " + clipName + " is not in playing list");
            delete this.playingSounds[clipName];
        }
    }

    resume(clipName: string) {
        if (!this.enable) return;

        clipName = clipName.toLowerCase();
        if (this.playingSounds[clipName] != undefined && cc.audioEngine.getState(this.playingSounds[clipName]) === cc.audioEngine.AudioState.PAUSED) {
            cc.audioEngine.resume(this.playingSounds[clipName]);
        } else {
            this.error("Resume sound: " + clipName + " is not in pause list");
            delete this.playingSounds[clipName];
        }
    }

    stop(clipName: string) {
        if (!this.enable) return;

        this.log("stop sound: " + clipName);
        clipName = clipName.toLowerCase();
        if (this.playingSounds[clipName] != undefined) {
            cc.audioEngine.stop(this.playingSounds[clipName]);
            delete this.playingSounds[clipName];
        } else {
            this.error("Stop sound: " + clipName + " is not in playing list");
        }
    }

    isPlaying(clipName: string): boolean {
        clipName = clipName.toLowerCase();
        if (this.playingSounds[clipName] != undefined) {
            let state = cc.audioEngine.getState(this.playingSounds[clipName]);
            return state == cc.audioEngine.AudioState.PLAYING;
        }
        return false;
    }

    pauseAll() {
        cc.audioEngine.pauseAll();
    }

    resumeAll() {
        if (!this.enable) return;

        cc.audioEngine.resumeAll();
    }

    stopAll() {
        if (!this.enable) return;

        cc.audioEngine.stopAll();
    }

    /**
     * set backgound music volume
     * @param volume from 0 - 1.0
     */
    setMusicVolume(volume: number) {
        if (!this.musicEnable) return;

        cc.audioEngine.setMusicVolume(volume);
    }

    /**
     * set effect sound volume
     * @param volume from 0 - 1.0
     */
    setEffectVolume(volume: number) {
        if (!this.effectEnable) return;

        cc.audioEngine.setEffectsVolume(volume);
    }

    /**
     * get the id of sound, to use with internal engin cc.audioEngine
     * @param clipName 
     */
    getSoundId(clipName: string): number {
        clipName = clipName.toLowerCase();
        return this.playingSounds[clipName];
    }
}
