import { ClientRequest } from './ClientRequest';
import Client, { ServerDesc } from '../multiplayer/client';
import PlayerRef, { DesignType } from "../../player-ref";


export default class ServerConnectionRequest extends ClientRequest
{
    m_desc: ServerDesc;
    m_onSuccessed: () => void;
    m_onFailed: (error) => void;
    m_force: boolean;

    public constructor(desc: ServerDesc, onSuccessed: () => void, onFailed: (error) => void = null, force: boolean = false)
    {
        super();
        this.m_desc = desc;
        this.m_onSuccessed = onSuccessed;
        this.m_onFailed = onFailed;
        this.m_force = force;
    }

    public start()
    {
        // console.log("ServerConnectionRequest - start - desc:" + this.m_desc );
        ClientRequest.ms_client = new Client(this);
        ClientRequest.ms_client.connectToHost(this.m_desc, this.m_onSuccessed, this.m_onFailed, this.m_force);
    }
}