    // gtm tracking
    static gtm_gameReturn() 
    {
        console.log("gtm_gameReturn()");
        let currentDayObject = new Date();
        let currentMilis: number = currentDayObject.getTime();

        let lastPlayInDays: any = Utils.Atob(cc.sys.localStorage.getItem(Utils.Btoa(window["productKey"] + "gameReturn_lastPlayInDays"))); 
        let currentPlayInDays: any = "";
		let firstDayInDays: any = Utils.Atob(cc.sys.localStorage.getItem(Utils.Btoa(window["productKey"] + "gameReturn_firstDayInDays"))); 
        
        let firstDayGTM: string = Utils.Atob(cc.sys.localStorage.getItem(Utils.Btoa(window["productKey"] + "gameReturn_firstDayGTM001"))); 
        let daysGTM: string = "Dxx"; 
        // default val
        
        if (firstDayGTM == null) // first time user play this game
        {
            firstDayInDays = lastPlayInDays = Math.ceil(currentMilis/(24*60*60*1000));
            firstDayGTM = "" + currentDayObject.getFullYear() + Utils.zeropad(currentDayObject.getMonth() + 1, 2) + Utils.zeropad(currentDayObject.getDate(), 2);
            daysGTM = "D00";

            cc.sys.localStorage.setItem(Utils.Btoa(window["productKey"] + "gameReturn_lastPlayInDays"), Utils.Btoa(lastPlayInDays)); 
            cc.sys.localStorage.setItem(Utils.Btoa(window["productKey"] + "gameReturn_firstDayGTM001"), Utils.Btoa(firstDayGTM));   
            cc.sys.localStorage.setItem(Utils.Btoa(window["productKey"] + "gameReturn_firstDayInDays"), Utils.Btoa(firstDayInDays));   
            
			if(Game.USE_NEW_PORTAL == true)
			{
				let ga_label = daysGTM + "-" + window["productTitle"];
				window["dataLayer"].push({
					'event': 'ga_event',
					'ga_category': 'Gamepage',
					'ga_action': 'Return',
					'ga_label': ga_label,
					'ga_noninteraction': true
					//'days': daysGTM
				});  
			}
			else
			{
				window["dataLayer"].push({
					'event': 'gameReturn',
					'ga_category': 'Gamepage',
					'ga_action': 'Return',
					'ga_label': window["productTitle"],
					'ga_noninteraction': true
					//'days': daysGTM
				}); 
			}                
        } 
        else { // from the 2nd time
            currentPlayInDays = Math.ceil(currentMilis/(24*60*60*1000));
            // currentPlayInDays++;
            if (currentPlayInDays > lastPlayInDays) // if >= 1 day
            {
                daysGTM = "D" + Utils.zeropad((currentPlayInDays - parseInt(firstDayInDays)), 2);

                lastPlayInDays = currentPlayInDays;
                cc.sys.localStorage.setItem(Utils.Btoa(window["productKey"] + "gameReturn_lastPlayInDays"), Utils.Btoa(lastPlayInDays)); 
                if(Game.USE_NEW_PORTAL == true)
				{
					let ga_label = daysGTM + "-" + window["productTitle"];
					window["dataLayer"].push({
						'event': 'ga_event',
						'ga_category': 'Gamepage',
						'ga_action': 'Return',
						'ga_label': ga_label,
						'ga_noninteraction': true
						//'days': daysGTM
					});  
				}
				else
				{
					window["dataLayer"].push({
						'event': 'gameReturn',
						'ga_category': 'Gamepage',
						'ga_action': 'Return',
						'ga_label': window["productTitle"],
						'ga_noninteraction': true
						//'days': daysGTM
					}); 
				}           
            }
        }
    }
