    onEditting() {
        if (!this.nameEditing) return;
        this.nameBox.string = this.nameBox.string.trim();
    }

    onEditedName(editBox: cc.EditBox) {
        if (!this.nameEditing) return;
        this.nameEditing = false; //@ done editing

        cc.log('onEditedName()')
        let badName = false;
        let lower = editBox.string.toLowerCase();
        if(editBox.string.length == 0 || lower.indexOf(' ') != -1) { // * Bug Fixed ['10588809']: '[HTML5][Settings] : User can use blank player name.
            badName = true;
        } else for (let lang in this.badWords.json) {
            if (this.badWords.json[lang]['blacklisted'].indexOf(lower) >= 0) {
                badName = true;
                break;
            }
        }

        if (badName) {
            Popup.instance.showInfoPopup(
                "Empty or Invalid character's name.\nPlease be polite in naming!", 
                () => {
                    editBox.string = editBox.placeholder = PlayerRef.current.name;
                    // editBox.setFocus(); // fix bug: Close the popup when user name is empty, the old name will be exit on Main Menu
                    this.nameEditing = true;
                }
            );
        } else {
            PlayerRef.current.name = editBox.string;
            this.mainGreeting.string = "Welcome " + editBox.string +"!";
            let items = cc.find("/board/content-control/character-name", this.popup).children;
            items[2].active = false; 
            items[0].children[0].getComponent(cc.Label).string = editBox.string;
            items[0].children[0].active = true;
            // items[1].active = true;
        }
    }

