    onTabButton(event, customEventData) {
        SoundManager.instance.playEffect("sfx_ui_select");
        if (this.tab != customEventData) {
            this.setPlayerRef();
            this.setupPopupContent(customEventData, event.target);
        }
    }

    onArrowButton(event, customEventData) {
        SoundManager.instance.playEffect("sfx_ui_next");
        let total = Math.abs(customEventData);
        if (total > 2) {
            if (customEventData > 0) { // to right
                if (++this.item == total) this.item = 0;
            }
            else if (customEventData < 0) { //to left
                if (--this.item < 0) this.item = (total - 1);
            }
        } else {
            this.item = 1 - this.item;
            let left = this.popup.children[1].children[3].children[0];
            left.active = this.item < 1;
            let right = this.popup.children[1].children[3].children[1];
            right.active = this.item > 0;
        }
        this.setupPopupContent(this.tab);
    }

    setupMainContent() {
        this.mainChar.play(this.mc_anim[+PlayerRef.current.user]);
        this.mainSkill.spriteFrame = this.contentFrames[+PlayerRef.current.user + Tabs.Skill * 3];
        this.mainGreeting.string = this.mainGreeting.string = "Welcome, " + PlayerRef.current.name +"!";
    }

    setPlayerRef() {
        if (this.tab == Tabs.Bomb) {
            PlayerRef.current.bomb = this.item;
        } else if (this.tab == Tabs.Character) {
            PlayerRef.current.user = this.item;
        } else if (this.tab == Tabs.Map) {
            PlayerRef.current.map = this.item;
        } else if (this.tab == Tabs.Control) {
            PlayerRef.current.control = this.item;
        }
        
        this.checkTutorial();

        this.setupMainContent();
    }

    getPlayerRef(): number {
        let item: number = -1;
        if (this.tab == Tabs.Bomb) {
            item = PlayerRef.current.bomb;
        } else if (this.tab == Tabs.Character) {
            item = PlayerRef.current.user;
        } else if (this.tab == Tabs.Map) {
            item = PlayerRef.current.map;
        } else if (this.tab == Tabs.Control) {
            item = PlayerRef.current.control;
        }
        
        return item;
    }

    setupPopupContent(tab: Tabs, target?: cc.Node) {
        if (this.tab < Tabs.Bomb || this.tab != tab) {
            this.tab = tab;
            this.item = this.getPlayerRef();
            let title = this.popup.children[2].children[0].getComponent(cc.Label);
            title.string = this.popup_title[tab];
        }
        if (this.tab < Tabs.Control) {
            let container = cc.find("/board/content-skin", this.popup);
            container.children[3].children.forEach((child, i) => { 
                child.getComponent(cc.Sprite).spriteFrame = (i == tab) ? this.tabFrames[i + 3] : this.tabFrames[i];
            })
            //container.children[0].active = !(this.tab == Tabs.Map);
            this.setupSkinContent(container);
        } else if(this.tab < Tabs.Servers) {
            this.setupControlContent();
        } else {
            this.setupServersContent(tab);
        }
    }

    setupSkinContent(container: cc.Node) {
        let center: number = +this.item;
        let left: number = center > 0 ? center - 1 : 2;
        let right: number = center < 2 ? center + 1 : 0;

        let chars:cc.Node[] = container.children[1].children;
        chars[0].getComponent(cc.Sprite).spriteFrame = this.contentFrames[left + this.tab * 3];
        chars[1].getComponent(cc.Sprite).spriteFrame = this.contentFrames[right + this.tab * 3];
        chars[2].getComponent(cc.Sprite).spriteFrame = this.contentFrames[center + this.tab * 3];
        
        let skill: cc.Node = container.children[2];
        if (this.tab == Tabs.Character) {
            skill.children[0].getComponent(cc.Sprite).spriteFrame = this.contentFrames[left + Tabs.Skill * 3];
            skill.children[1].getComponent(cc.Sprite).spriteFrame = this.contentFrames[right + Tabs.Skill * 3];
            skill.children[2].getComponent(cc.Sprite).spriteFrame = this.contentFrames[center + Tabs.Skill * 3];
            skill.active = true;
        } else {
            skill.active = false;
        }
    }

    setupControlContent() {
        let name = cc.find("/board/content-control/character-name/name-bg/name", this.popup);
        name.getComponent(cc.Label).string = PlayerRef.current.name;

        if(cc.sys.isMobile)
        {
            let desc = cc.find("board/content-control/control-type/desc", this.popup);
            desc.getComponent(cc.Label).string = "USE D-PAD TO MOVE JELLY";
        }

        let type = cc.find("board/content-control/control-type/popup-control-type/image-desc", this.popup);
        type.getComponent(cc.Sprite).spriteFrame = this.controlFrames[this.item];
    
        this.popup.children[1].children[3].children[0].active = !cc.sys.isMobile && this.item < 1;
        this.popup.children[1].children[3].children[1].active = !cc.sys.isMobile && this.item > 0;
    }

