import { BitStream } from "./BitStream";

// simple json compress to ArrayBuffer

enum Type {
    Bit,
    UInt2,
    UInt3,
    UInt4,
    UInt5,
    UInt6,
    UInt7,
    UInt8,
    UInt9,
    UInt10,
    UInt11,
    UInt12,
    UInt13,
    UInt14,
    UInt15,
    UInt16,
    UInt32,
    String,
    Binary,
    PrimativeType,      // separate, primitive type can be read/write in 1 action
    Object,
    Array,
}
// types to bits map
let TypeSize = {
    // base
    [Type.Bit]: 1,
    [Type.UInt2]: 2,
    [Type.UInt3]: 3,
    [Type.UInt4]: 4,
    [Type.UInt5]: 5,
    [Type.UInt6]: 6,
    [Type.UInt7]: 7,
    [Type.UInt8]: 8,
    [Type.UInt9]: 9,
    [Type.UInt10]: 10,
    [Type.UInt11]: 11,
    [Type.UInt12]: 12,
    [Type.UInt13]: 13,
    [Type.UInt14]: 14,
    [Type.UInt15]: 15,
    [Type.UInt16]: 16,
    [Type.UInt32]: 32,
    [Type.String]: 8,      // 8 bits for string length
    [Type.Object]: 0,
    [Type.Array]: 8,     // 8 bits for array length
    [Type.Binary]: 10,      // support to 2^10 = 1024Byte
}

// template for socket cluster message
let scProto = {
    maxIdx: 5,
    template: {
        emit: {
            index: 1,
            type: Type.Object,
            template: {
                event: { index: 1, type: Type.String },
                data: { index: 2, type: Type.Binary },
                cid: { index: 5, type: Type.UInt16 },
            }
        },
        publish: {
            index: 2,
            type: Type.Object,
            template: {
                data: { index: 2, type: Type.Binary },
                channel: { index: 3, type: Type.String },
                cid: { index: 5, type: Type.UInt16 },
            }
        },
        response: {
            index: 3,
            type: Type.Object,
            template: {
                data: { index: 2, type: Type.Binary },
                rid: { index: 4, type: Type.UInt16 },
            }
        },
    },
}

export class JsonPacker {
    private proto;
    private stream: BitStream;
    private indexBit: number;

    constructor() {
        this.setProto(scProto);
    }

    private writePrimitive(type: Type, data) {
        if (type < Type.PrimativeType) {
            if (type == Type.String) {
                if (data && typeof (data) === 'string') {
                    this.stream.writeUint8(data.length);
                    this.stream.writeASCIIString(data, data.length);
                } else {
                    console.warn("Not valid string type");
                    this.stream.writeUint8(0x00);
                }
            } else if (type == Type.Binary) {
                if (data instanceof ArrayBuffer) {
                    this.stream.writeBits(data.byteLength, 10);
                    this.stream.writeArrayBuffer(data, data.byteLength)
                } else {
                    console.warn("Not valid binary type ArrayBuffer");
                    this.stream.writeBits(0x00, 10);
                }
            } else {
                this.stream.writeBits(data, TypeSize[type]);
            }
        } else {
            console.warn("Do not support primitive type: " + type);
        }
    }
    private write(input, template) {
        for (let key in input) {
            let proto = template[key];
            if (proto) {
                this.stream.writeBits(proto.index, this.indexBit);
                if (proto.type < Type.PrimativeType) {
                    this.writePrimitive(proto.type, input[key]);
                } else if (proto.type == Type.Object) {
                    this.write(input[key], proto.template);
                    this.stream.writeBits(0x00, this.indexBit);
                } else {
                    console.warn(`Not support write type: ${proto.type}`)
                }
            } else {
                console.warn(`Key [${key}] not found in template`)
            }
        }
    }

    private readPrimitive(type: Type) {
        if(type < Type.PrimativeType) {
            if(type == Type.String) {
                let length = this.stream.readUint8();
                return length == 0 ? null : this.stream.readASCIIString(length);
            } else if (type == Type.Binary) {
                let length = this.stream.readBits(10);
                return this.stream.readArrayBuffer(length);
            } else {
                return this.stream.readBits(TypeSize[type]);
            }
        }
        return null;
    }

    private read(template) {
        let out = {}
        while (this.stream.bitsLeft > this.indexBit) {
            let keyIdx = this.stream.readBits(this.indexBit);
            if (keyIdx == 0x00) break;
            
            let key = template.keyIndex_[keyIdx];
            if (template[key].type < Type.PrimativeType) {
                let value = this.readPrimitive(template[key].type);
                out[key] = value;
            } else if (template[key].type == Type.Object) {
                out[key] = this.read(template[key].template);
            } else {
                console.warn(`Not support read type: ${template[key].type}`)
            }
        }
        return out;
    }

    private updateKeyIndex(object) {
        if (object.template) {
            object.template.keyIndex_ = {};
            for (let key in object.template) {
                let proto = object.template[key];
                object.template.keyIndex_[proto.index] = key;

                if (proto.template) {
                    this.updateKeyIndex(proto);
                }
            }
        }
    }

    setProto(proto): JsonPacker {
        if (proto.maxIdx && proto.template) {
            this.proto = proto;
            this.indexBit = Math.floor(Math.log2(proto.maxIdx)) + 1;
            // prepair key index
            this.updateKeyIndex(proto);
            return this;
        }
        console.warn("Invalid proto");
        return null;
    }

    pack(input): ArrayBuffer {
        this.stream = new BitStream(new ArrayBuffer(512));
        this.write(input, this.proto.template);
        return this.stream.view.getArrayBuffer(0, this.stream.byteIndex);
    }

    unpack(input: ArrayBuffer) {
        this.stream = new BitStream(input);
        let obj = this.read(this.proto.template);
        return obj;
    }
}