import LabelLocalized from "./LabelLocalized";

const {ccclass, property, requireComponent, executeInEditMode, menu} = cc._decorator;

@ccclass
@requireComponent(cc.RichText)
@menu('i18n:MAIN_MENU.component.renderers/RichTextLocalized')
@executeInEditMode
export default class RichTextLocalized extends LabelLocalized {

    onLoad() {
        this.label = this.getComponent(cc.RichText);
        this.updateLabel();
    }

    updateLabel () {
        if(this.label == null) {
            this.label = this.getComponent(cc.RichText);
        }
        super.updateLabel();
    }
}