import { SequenceNodeShowPopup, SequenceNodeShowPopupState } from './SequenceNodeShowPopup'
import MainMenu from '../menu/main-menu'

const {ccclass, property} = cc._decorator;

@ccclass
export abstract class SequenceNodeShowSelectSkinPopup extends SequenceNodeShowPopup
{
    @property(cc.Node)
    selectSkinPopup: cc.Node = null;

    @property(MainMenu)
    main_menu: MainMenu = null;

    m_closeButton: cc.Button;
    m_closeEvent: number;
    
    protected SetupGUI()
    {
        this.m_closeButton = this.selectSkinPopup.children[1].getChildByName("close").getComponent(cc.Button);
    }

    protected CreateConnection()
    {console.log("SequenceNodeShowSelectSkinPopup - CreateConnection");
        var clickEventHandler = new cc.Component.EventHandler();
        clickEventHandler.target = this.node; // This node is the node to which your event handler code component belongs
        clickEventHandler.component = "SequenceNodeShowSelectSkinPopup";// This is the code file name
        clickEventHandler.handler = "OnCloseButtonPressed";

        this.m_closeEvent = this.m_closeButton.clickEvents.push(clickEventHandler);
    }

    public Finish()
    {
        this.m_closeButton.clickEvents.splice(this.m_closeEvent, 1);
    }
}
