// remote player control class

import Player, { PlayerAction } from "./player";
import { PlayerInfo, LbInfo } from "./multiplayer/shared/IMessageData";
import Game from "./game";
const { ccclass, property, requireComponent } = cc._decorator;

@ccclass
@requireComponent(Player)
export default class PlayerControl extends cc.Component {

    protected player: Player;
    protected serverData: PlayerInfo[] = [];
    private positionBuffer: [cc.Vec2, number][] = [];

    onLoad() {
        this.player = this.getComponent(Player);
        this.player.controller = this;
    }

    start() {

    }

    setServerData(playerInfo: PlayerInfo) {
        if (!Game.isTutorial)
            this.serverData.push(playerInfo);
    }

    update(dt: number) {
        if (!Game.isTutorial)
        {
            this.processServerData();
            this.processPosition();
        }
        else{
            this.processPosition();
        }
    }

    processServerData() {
        while (this.serverData.length > 0) {
            let data = this.serverData.shift();
            this.player.setServerData(data);
            this.positionBuffer.push([cc.v2(data.x, data.y), Date.now()]);
        }
    }

    handleDeath(): boolean {
        return false;
    }

    private processPosition() {     // interpolate remote player pos
        let renderTime = Date.now() - (1000.0 / 30);    // 30 = server fps

        while (this.positionBuffer.length >= 2 && this.positionBuffer[1][1] <= renderTime) {
            this.positionBuffer.shift();
        }

        // Interpolate between the two surrounding authoritative positions.
        if (this.positionBuffer.length >= 2 && this.positionBuffer[0][1] <= renderTime && renderTime <= this.positionBuffer[1][1]) {
            let from: cc.Vec2 = this.positionBuffer[0][0];
            let to: cc.Vec2 = this.positionBuffer[1][0];
            let t0 = this.positionBuffer[0][1];
            let t1 = this.positionBuffer[1][1];

            let pos = from.lerp(to, (renderTime - t0) / (t1 - t0));
            this.player.setPosition(pos.x, pos.y);
        }
    }

    updateRank(ranks: LbInfo[], localPlayer: boolean = false) {
        let top = 0;
        for (let i = 0; i < 3 && i < ranks.length; i++) {        // first 3 ranks
            if (this.player.id == ranks[i].id) {
                top = i + 1;
            }
        }
        this.player.setStatus(top, localPlayer);
    }
}