import { SequenceNode } from './SequenceNode'

const {ccclass, property} = cc._decorator;

@ccclass
export default class SequenceNodeWait extends SequenceNode
{
    @property
    delay: number = 0;

    private m_counter: number = 0;
    
    public UpdateNode(dt: number)
    {//console.log("SequenceNodeShowPopup - UpdateNode - m_counter: " + this.m_counter + " - delay: " + this.delay);
        this.m_counter += dt;
    }

    public IsFinished(): boolean { return this.m_counter >= this.delay; }

    public Finish() {}
}
