import EventManager from "../../core/event-manager";
import Player from "../player";
import SoundManager from "../../core/sound-manager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class LevelExpControl extends cc.Component {
    @property(cc.Label)
    text: cc.Label = null;

    private expBar: cc.ProgressBar = null;

    start() {
        this.expBar = this.getComponent(cc.ProgressBar);
        EventManager.instance.register(Player.EVENT_LOCAL_PLAYER_EXP_CHANGE, this.onExpUpdate, this);
    }

    onExpUpdate(level: number, exp: number) {
        let levelExp = Player.getLevelExp(level);
        this.expBar.progress = exp / levelExp;
        this.text.string = "Lvl: " + level + " / Exp: " + exp;
    }

    onDestroy() {
        EventManager.instance.unregisterTarget(this);
    }

}