import Game from "../game/game";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Popup extends cc.Component {

    private static _instance: Popup = null;
    public static get instance(): Popup {
        if(this._instance) return this._instance;
        cc.error("Popup does not init yet, this need to be init in loader");
        return null;
    }

    @property(cc.Prefab)
    dialogPopup: cc.Prefab = null;

    @property(cc.Prefab)
    waitingPopup: cc.Prefab = null;

    private target: cc.Node = null;

    onLoad() {
        Popup._instance = this;
        cc.game.addPersistRootNode(this.node);
        this.node.zIndex = 1000;
    }

    setTarget(target: cc.Node) {
        this.target = target;
    }

    showInfoPopup(content: string, closeCallback?: Function, hasConfirmation?: boolean, cancelCallback?: Function) {
        let canvas = this.target ? this.target : cc.find("Canvas");
        let node = cc.instantiate(this.dialogPopup);
        node.name = "final_Info_dialog";
        let blockInput = node.getChildByName('blockinput').getComponent(cc.Widget);
        blockInput.target = canvas; blockInput.left = 0; blockInput.right = 0;
        node.getChildByName("confirm-content").active = false;
        let info = node.getChildByName("info-content");
        content && (info.getChildByName("title").getComponent(cc.Label).string = content);
        info.getChildByName("title").getComponent(cc.Label).horizontalAlign = 1;
        info.getChildByName("title").getComponent(cc.Label).verticalAlign = 1;
        info.active = true;

        let cancel: cc.Button = info.getChildByName("cancelbutton").getComponent(cc.Button);
        cancel.node.on('click', () => {
            node.removeFromParent();
            if(closeCallback) closeCallback();
        })

        let positive: cc.Button = info.getChildByName("positivebutton").getComponent(cc.Button);
        positive.node.on('click', () => {
            node.removeFromParent();
            if(closeCallback) closeCallback();
        })

        let negative: cc.Button = info.getChildByName("negativebutton").getComponent(cc.Button);
        negative.node.on('click', () => {
            node.removeFromParent();
            if(closeCallback && !hasConfirmation) closeCallback();
            if(cancelCallback && hasConfirmation) cancelCallback();
        })
        
        if (hasConfirmation) {
            cancel.node.active = false;
        } else {
            positive.node.active = false;
            negative.node.active = false;
        }

        canvas.addChild(node);
    }

    showConfirmPopup(mc: cc.SpriteFrame, played_time: string, kill_no: string, positiveCallback?: Function, negativeCallback?: Function) {
        let canvas = this.target ? this.target : cc.find("Canvas");
        let node = cc.instantiate(this.dialogPopup);
        let blockInput = node.getChildByName('blockinput').getComponent(cc.Widget);
        blockInput.target = canvas; blockInput.left = 0; blockInput.right = 0;
        node.getChildByName("info-content").active = false;
        let content = node.getChildByName("confirm-content");
        content.active = true;
        content.getChildByName("character").getComponent(cc.Sprite).spriteFrame = mc;
        content.getChildByName("played-time").getComponent(cc.Label).string = played_time;
        content.getChildByName("killed-number").getComponent(cc.Label).string = kill_no;
        let button1: cc.Button = content.getChildByName("positivebutton").getComponent(cc.Button);
        button1.node.on('click', ()=>{
            node.removeFromParent();
            if(positiveCallback) positiveCallback();
        });
        let button2: cc.Button = content.getChildByName("negativebutton").getComponent(cc.Button);
        button2.node.on('click', ()=>{
            node.removeFromParent();
            if(negativeCallback) negativeCallback();
        });
        canvas.addChild(node); 
    }

    private waitingPopupNode: cc.Node = null;
    showWaitingPopup(content: string) {
        if (!this.waitingPopupNode) {
            let canvas = this.target ? this.target : cc.find("Canvas");
            let node = cc.instantiate(this.waitingPopup);
            let blockInput = node.getChildByName('blockinput').getComponent(cc.Widget);
            blockInput.target = canvas; blockInput.left = 0; blockInput.right = 0;
            this.waitingPopupNode = node;
            canvas.addChild(this.waitingPopupNode);
            this.waitingPopupNode.getChildByName("content").getComponentInChildren(cc.Label).string = content;
        }
    }

    hideWaitingPopup() {
        this.waitingPopupNode.removeFromParent();
    }

    showNotify(content: string, duration: number = 3) {
        let canvas = this.target ? this.target : cc.find("Canvas");
        let node = cc.instantiate(this.waitingPopup);
        node.getChildByName("blockinput").removeFromParent();
        node.getChildByName("content").getComponentInChildren(cc.Label).string = content;
        canvas.addChild(node);
        this.scheduleOnce(()=>{
            node.removeFromParent();
        }, duration);
    }

}