import { SequenceNode } from './SequenceNode'
import { SequenceNodeShowPopupState } from './SequenceNodeShowPopup';
import PlayerRef from "../../player-ref";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SequenceManager extends cc.Component
{
    @property(SequenceNode)
    sequences: SequenceNode[] = [];

    private m_nodeIdx: number = 0;

    // private static ms_instance: SequenceManager;

    onLoad()
    {
        // SequenceManager.ms_instance = this;console.log("SequenceManager - onLoad - " + PlayerRef.current.first_launch_step);
    }
    
    update (dt) { this.updateNode(dt); }

    // public GetInstance() { return SequenceManager.ms_instance; }

    public push(node: SequenceNode) { this.sequences.push(node); }

    public updateNode(dt: number)
    {
        if(this.m_nodeIdx < this.sequences.length)
        {
            var node = this.sequences[this.m_nodeIdx];
            node.UpdateNode(dt);
            if(node.IsFinished())   this.m_nodeIdx++;
        }
    }
}
