import TransportMessage from "./transport-message";
import PlayerRef from "../player-ref";
import EventManager from "./event-manager";
import SoundManager from "./sound-manager";
import Localization from "./localization";
import Cacher from "../game/resources-cacher";

// this will load loader.scene to init all our needed for game
// this script need attach to each scene 1 time (in canvas obj)
// we can also attach more manager to 'loader' scene then listen to it init complete

const {ccclass, property, executionOrder} = cc._decorator;

@ccclass
@executionOrder(-100)
export default class Loader extends cc.Component {

    public static loaded: boolean = false;

    private nextScene = "main-menu";

    @property
    targetLoad: string = "";

    onLoad() {
        // check if loader is load before
        if(Loader.loaded == false) {            
            if(this.getCurrentSceneName() != 'loader') {
                let nextScene = this.targetLoad != "" ? this.targetLoad : this.getCurrentSceneName();
                TransportMessage.instance.send('loader', 'nextScene', nextScene);
                cc.director.loadScene('loader');
            } else {
                Loader.loaded = true;
                let next = TransportMessage.instance.get('loader', 'nextScene');
                this.nextScene = next != null ? next : this.nextScene;
            }
        }
    }

    start() {
        if(this.getCurrentSceneName() == 'loader') {
            this.initLoader();
        }
    }

    // lib need to load before game
    private _loadInstances = {
        "PlayerRef": false,
        "Sound": false,
        "Character": false,
    }
    private initLoader() {
        
        // listen on manager init done
        EventManager.instance.registerOnce('PlayerRef-init', ()=>{
            this._loadInstances["PlayerRef"] = true;
        }, this)
        EventManager.instance.registerOnce('Sound_initialized', ()=>{
            this._loadInstances["Sound"] = true;
        }, this)
        EventManager.instance.registerOnce('Resources-Cached', () => {
            this._loadInstances["Character"] = true;
        })
        // init other manager
        PlayerRef.current; 
        Cacher.instance.load();
        SoundManager.instance.init();
        if (Localization.getCurrentLanguage() == null) {
            Localization.setLanguage(cc.sys.language);
        }
    }

    private _loadGame = false;
    update(dt) {
    // goNext() {
        if(PlayerRef.current.initialized) this._loadInstances["PlayerRef"] = true;
        
        let allLoaded = true;
        for(let inst in this._loadInstances) {
            if(this._loadInstances[inst] == false) {
                allLoaded = false;
                break;
            }
        }

        if(allLoaded && !this._loadGame) {
            // start next scene when all complete
            this._loadGame = true;
            cc.director.loadScene(this.nextScene);
        }
    }

    private getCurrentSceneName(): string {
        let scene: string = "";
        (cc.game as any)._sceneInfos.forEach(element => {
            if(element.uuid == (cc.director as any)._scene._id) {
                scene = element.url;
                scene = scene.substring(scene.lastIndexOf('/')+1).match(/[^\.]+/)[0]
            }
        });

        return scene;
    }
}