import Game from "./game";
import { Utils } from "../utils";
import Tutorial from "./tutorial";
const { ccclass, property } = cc._decorator;

@ccclass
export default class CameraControl extends cc.Component {

    static instance: CameraControl = null;

    @property(cc.Node)
    targetPlayer: cc.Node = null;

    
    camera: cc.Camera = null;

    private followPlayer: boolean = true;
    private _dgMoveDirect: cc.Vec2 = cc.Vec2.ZERO;
    private _dgCamSpeed = 300;
    private _dgCamZoom = 0;
    private _dgKeys = {};

    private _offset = 120;

    start() {
        CameraControl.instance = this;

        // register event
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);

        this.camera = this.getComponent(cc.Camera);
    }

    moveToTarget() {
        if (this.targetPlayer) {
            let target = this.adjustPos(this.targetPlayer.position);
            this.node.setPosition(target.x, target.y);
        }
    }


    update(dt: number) {
        if (this.followPlayer && this.targetPlayer) {
            let target = this.adjustPos(this.targetPlayer.position);

            // smooth follow
            if (Game.isTutorial)
            {
				if (Tutorial.instance)
				{
                let x = Utils.moveTowards(this.node.x, target.x, Tutorial.instance.playerSpeed * dt);
                let y = Utils.moveTowards(this.node.y, target.y, Tutorial.instance.playerSpeed * dt);
                this.node.setPosition(x, y);
				}
            }
            else
            {
				if (Game.instance)
				{
                let x = Utils.moveTowards(this.node.x, target.x, Game.instance.playerSpeed * dt);
                let y = Utils.moveTowards(this.node.y, target.y, Game.instance.playerSpeed * dt);
                this.node.setPosition(x, y);
				}
            }

        } else {
            if (this._dgMoveDirect.x != 0 || this._dgMoveDirect.y != 0) {
                // move debug cam
                let multiply = 1;       // use for speed on diagonal
                if (this._dgMoveDirect.x != 0 && this._dgMoveDirect.y != 0) {
                    multiply = 1.4;     // sqrt(2) ~ 1.4
                }
                let dx = this._dgMoveDirect.x * multiply * this._dgCamSpeed * dt / this.camera.zoomRatio;
                let dy = this._dgMoveDirect.y * multiply * this._dgCamSpeed * dt / this.camera.zoomRatio;
                this.node.setPosition(this.node.position.add(cc.v2(dx, dy)));
            }

            if (this._dgCamZoom != 0) {
                this.camera.zoomRatio += this._dgCamZoom * this.camera.zoomRatio * 0.05;
            }
        }
    }

    private adjustPos(target: cc.Vec2): cc.Vec2 {
        // check bound
        let l = target.x - this.node.width / 2;
        let r = target.x + this.node.width / 2;
        let t = target.y + this.node.height / 2;
        let b = target.y - this.node.height / 2;

        if (Game.isTutorial)
        {
			if (Tutorial.instance)
			{
            if (l < -this._offset) target.x = this.node.width / 2 - this._offset;
            if (r > Tutorial.instance.worldSize.x + this._offset) target.x = Tutorial.instance.worldSize.x - this.node.width / 2 + this._offset;
            if (b < -this._offset) target.y = this.node.height / 2 - this._offset;
            if (t > Tutorial.instance.worldSize.y + this._offset) target.y = Tutorial.instance.worldSize.y - this.node.height / 2 + this._offset;
			}
        }
        else
        {
			if (Game.instance)
			{
            if (l < -this._offset) target.x = this.node.width / 2 - this._offset;
        if (r > Game.instance.worldSize.x + this._offset) target.x = Game.instance.worldSize.x - this.node.width / 2 + this._offset;
        if (b < -this._offset) target.y = this.node.height / 2 - this._offset;
        if (t > Game.instance.worldSize.y + this._offset) target.y = Game.instance.worldSize.y - this.node.height / 2 + this._offset;
			}
        }

        return target;
    }

    setFollow(follow: boolean) {
        this.followPlayer = follow;
        if (follow) {
            this.node.setPosition(this.adjustPos(this.targetPlayer.position));
        }
    }

    setTarget(target: cc.Node) {
        this.targetPlayer = target;
        this.setFollow(this.followPlayer);
    }

    getTarget() {
        return this.targetPlayer;
    }

    onKeyUp(event: cc.Event.EventKeyboard) {
        this._dgKeys[event.keyCode] = 0;
        if (event.keyCode == cc.macro.KEY.up) {
            this._dgMoveDirect.y = 0;
        } else if (event.keyCode == cc.macro.KEY.down) {
            this._dgMoveDirect.y = 0;
        } else if (event.keyCode == cc.macro.KEY.left) {
            this._dgMoveDirect.x = 0;
        } else if (event.keyCode == cc.macro.KEY.right) {
            this._dgMoveDirect.x = 0;
        }
        // zoom
        else if (event.keyCode == 109) {     // key '+'
            this._dgCamZoom = 0;
        } else if (event.keyCode == 107) {     // key '-'
            this._dgCamZoom = 0;
        }
    }

    onKeyDown(event: cc.Event.EventKeyboard) {
        this._dgKeys[event.keyCode] = 1;
        // move
        if (event.keyCode == cc.macro.KEY.up) {
            this._dgMoveDirect.y = 1;
        } else if (event.keyCode == cc.macro.KEY.down) {
            this._dgMoveDirect.y = -1;
        } else if (event.keyCode == cc.macro.KEY.left) {
            this._dgMoveDirect.x = -1;
        } else if (event.keyCode == cc.macro.KEY.right) {
            this._dgMoveDirect.x = 1;
        }
        // zoom
        else if (event.keyCode == 109) {     // key '+'
            this._dgCamZoom = 1;
        } else if (event.keyCode == 107) {     // key '-'
            this._dgCamZoom = -1;
        } else if (event.keyCode == 110) {     // key '.'
            this.camera.zoomRatio = 1;
        }

        // focus or not
        if (event.keyCode == cc.macro.KEY.f && this._dgKeys[cc.macro.KEY.shift]) {
            this.setFollow(!this.followPlayer);
        }
    }
}