import { SequenceNode } from './SequenceNode'

const {ccclass, property} = cc._decorator;

export enum SequenceNodeShowPopupState
{
    INITIALIZED,
    STARTED,
    FINISHED,
}

@ccclass
export abstract class SequenceNodeShowPopup extends SequenceNode
{
    protected m_state: SequenceNodeShowPopupState = SequenceNodeShowPopupState.INITIALIZED;

    public UpdateNode(dt: number)
    {//console.log("SequenceNodeShowPopup - UpdateNode - state: " + this.m_state);
        if(this.m_state == SequenceNodeShowPopupState.INITIALIZED)
        {
            this.SetupGUI();
            this.CreateConnection();
            this.ShowPopup();
            this.m_state = SequenceNodeShowPopupState.STARTED;
        }
    }

    public IsFinished(): boolean { return this.m_state == SequenceNodeShowPopupState.FINISHED; }

    public OnCloseButtonPressed(event, customEventData)
    {console.log("SequenceNodeShowPopup - OnCloseButtonPressed");
        this.m_state = SequenceNodeShowPopupState.FINISHED;
    }

    protected abstract SetupGUI(): void;

    protected abstract CreateConnection(): void;

    public abstract ShowPopup(): void;
}
