import { Utils } from "../utils";

export interface InputData {
    direction?: number;
    fire?: number; //0: idle, 1: normal attack, 2: special attack
    fire_dir?: number;
    fire_range?: number;
}

export default class Input {
    private static _instance: Input = null;
    public static get instance(): Input {
        if (Input._instance == null) {
            Input._instance = new Input();
        }
        return Input._instance;
    }

    private inputSources: { [key: number]: InputData } = {};

    set(data: InputData, weight: number) {
        if (this.inputSources[weight]) data = Utils.extend(this.inputSources[weight], data);
        this.inputSources[weight] = data;
    }

    clear() {
        this.inputSources = {};
    }

    getData(): InputData {
        let data = null;
        for (let weight in this.inputSources) {
            data = Utils.extend(data, this.inputSources[weight]);
        }
        this.inputSources = {};
        return data;
    }
}