import Game from "./game"
import CameraControl from "./camera-control";
import PlayerRef, { ControlType } from "../player-ref";

import Input, { InputData } from "./input";
import { Utils } from "../utils";
import Tutorial, { TUTORIAL_STATE, TUTORIAL_SUB_STATE } from "./tutorial";
import { PlayerAction } from "./player";

const { ccclass, property, requireComponent } = cc._decorator;

@ccclass
export default class TouchInput extends cc.Component {

    @property(cc.Node)
    touchZone: cc.Node = null;

    @property(cc.Node)
    dpad: cc.Node = null;

    @property(cc.Node)
    buttonFire: cc.Node = null;

    @property(cc.Node)
    buttonSkill: cc.Node = null;

    private angle = -1;
    private startPos: cc.Vec2 = null;
    private defaultDpadPos: cc.Vec2 = null;

    private fireCenterPos: cc.Vec2 = null;
    private fireInfo: {fire: number, fire_dir: number, fire_range: number} = null;
    
    private buttonZIndex = 0;
    

    start() {
        if (PlayerRef.current.control == ControlType.DPad) {
            this.dpad.active = true;
            this.defaultDpadPos = this.dpad.position;
            
            //@ align touch zone to left side
            let wg = this.touchZone.getComponent(cc.Widget);
            wg.right = 480;
            wg.updateAlignment();
        }
        
        [this.touchZone, this.buttonFire, this.buttonSkill].forEach(zone => {
            zone.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
            zone.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
            zone.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
            zone.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);    
        })
    }

    update(dt: number) {
        let info = null;
        // movement
        if(this.startPos) {
            info = Utils.extend(info, {direction: Math.round(this.angle)});
        }
        // target indicator is moving
        if (this.fireCenterPos) {
            info = Utils.extend(info, this.fireInfo);
        }

        if (Game.isTutorial)
        {
            // cc.log('USE_DYNAMIC_BOMB -- update()');
            if(Game.pressed_btnfire)
            {
                Game.dynamic_range += Game.count;
                if(Game.dynamic_range >= Game.max_range)
                {
                    Game.dynamic_range = Game.max_range;
                }
                Tutorial.instance.updateLocalTarget(0, Game.dynamic_range);  
            }
            else
            {
                Game.dynamic_range = Game.min_range;
                Tutorial.instance.updateLocalTarget(0, 0);             
            }
        }
        else
        {
            // if(Game.USE_DYNAMIC_BOMB && !Game.isTutorial && Game.instance && Game.instance.localPlayer)
            if(!Game.USE_DYNAMIC_BOMB && Game.instance && Game.instance.localPlayer)
            { 
                // cc.log('USE_DYNAMIC_BOMB -- update()');
                if(Game.pressed_btnfire)
                {
                    Game.dynamic_range += Game.count;
                    if(Game.dynamic_range >= Game.max_range)
                    {
                        Game.dynamic_range = Game.max_range;
                    }
                    Game.instance.updateLocalTarget(Game.instance.localPlayer.direction, Game.dynamic_range);  
                }
                else
                {
                    Game.dynamic_range = Game.min_range;
                    Game.instance.updateLocalTarget(0, 0);             
                }
            }
        }

        // already fired -> reset the fire info        
        if (!Game.isTutorial)
        {
            if (Game.instance)
                if (Game.instance.localPlayer)
                    if (this.fireInfo && Game.instance.localPlayer.isFiring) {
                        this.fireInfo = null;
                        info = Utils.extend(info, {fire: 0, fire_dir: -1, fire_range: 0});
                    }
        }
        else
        {
            if (this.fireInfo && Tutorial.instance.TutorialPlayer.isFiring) {
                this.fireInfo = null;
                info = Utils.extend(info, {fire: 0, fire_dir: -1, fire_range: 0});
            }
        }

        if (Game.isTutorial)
        {
            if (Tutorial.instance)
                if (Tutorial.instance.performAction == 3)
                    return;
                else
                    info && this.setInput(info);
            else
                info && this.setInput(info);
        }
        else
        {   info && this.setInput(info);}
    }

    setInput(data: InputData) {
        Input.instance.set(data, 1);        // touch weight: 1, keyboard weight: 0
    }

    //touch event
    onTouchStart(event) {
        if (event.target == this.touchZone) {
            this.startPos = event.getLocation();
            if (Game.isTutorial 
                && (Tutorial.tutorial_status == TUTORIAL_STATE.Tutorial_shield || Tutorial.tutorial_status == TUTORIAL_STATE.Tutorial_skill)
                && Tutorial.tutorial_sub_status == TUTORIAL_SUB_STATE.Tutorial_checkdoAction
                && Tutorial.instance.performAction == 2
                )
            {
                Tutorial.instance.performAction = 3;
            }
            if (Game.isTutorial)
                if (Tutorial.instance)
                    if (Tutorial.instance.performAction == 3)
                        return;

            if (this.dpad.active) {
                if (Game.isTutorial)
                {
                    if (Tutorial.tutorial_status != TUTORIAL_STATE.Tutorial_skill)
                    {
                        let touchPos = this.dpad.parent.convertToNodeSpaceAR(this.startPos);
                        this.dpad.setPosition(touchPos);
                    }
                }
                else
                {
                    let touchPos = this.dpad.parent.convertToNodeSpaceAR(this.startPos);
                    this.dpad.setPosition(touchPos);
                }
            }
        }

        if (event.target == this.buttonFire)
        {
            if(!Game.USE_DYNAMIC_BOMB)
            {
                this.buttonFire.getChildByName("indicator").setScale(0.8);
                Game.pressed_btnfire = true;  
            }
            else
            {
                this.buttonZIndex = event.target.zIndex;
                event.target.zIndex = cc.macro.MAX_ZINDEX;
                let center = event.target.convertToWorldSpaceAR(cc.Vec2.ZERO);
                this.fireCenterPos = center;
            }
        }
        else if (event.target == this.buttonSkill)
        {
            this.buttonZIndex = event.target.zIndex;
            event.target.zIndex = cc.macro.MAX_ZINDEX;
            let center = event.target.convertToWorldSpaceAR(cc.Vec2.ZERO);
            this.fireCenterPos = center;
        }
    }

    onTouchMove(event) {
        if (event.target == this.touchZone) {
            if (this.startPos) {
                let dx, dy;
                if (this.dpad.active) {
                    dy = event.getLocationY() - this.startPos.y;
                    dx = event.getLocationX() - this.startPos.x;
                } else if (this.touchZone.active) {
                    let touchPos = CameraControl.instance.camera.getScreenToWorldPoint(event.getLocation(), null);
                    if (!Game.isTutorial)
                    {
                        dy = touchPos.y - Game.instance.localPlayer.node.position.y;
                        dx = touchPos.x - Game.instance.localPlayer.node.position.x;
                    }
                    else
                    {
                        dy = touchPos.y - Tutorial.instance.TutorialPlayer.node.position.y;
                        dx = touchPos.x - Tutorial.instance.TutorialPlayer.node.position.x;
                    }
                }
                this.angle = Math.atan2(dy, dx);

                //@ update dpad position  
                if (this.dpad.active) {
                    if (dx*dx + dy*dy > 100*100) {
                        dx = Math.round(Math.cos(this.angle) * 100);
                        dy = Math.round(Math.sin(this.angle) * 100);
                    }

                    if (Game.isTutorial)
                        if (Tutorial.instance)
                            if (Tutorial.instance.performAction == 3)
                                return;

                    this.dpad.getChildByName("indicator").setPosition(dx, dy);
                }

                //@ convert to degree
                this.angle *= 180 / Math.PI;
                if (this.angle < 0) this.angle += 360;            
            }
        }

        // Nhan note: for active skill(old version)
        //if (event.target == this.buttonFire || event.target == this.buttonSkill) {
        if (event.target == this.buttonFire && Game.USE_DYNAMIC_BOMB) {
            let dx = event.getLocationX() - this.fireCenterPos.x;
            let dy = event.getLocationY() - this.fireCenterPos.y;
            let angle = Math.atan2(dy, dx);
            let range = Math.sqrt(dx*dx + dy*dy);

            if ( range > 100 ) {
                range = 100;
                dx = Math.round(Math.cos(angle) * 100);
                dy = Math.round(Math.sin(angle) * 100);
            }
            event.target.getChildByName("indicator").setPosition(dx, dy);
            
            angle *= 180 / Math.PI;
            if (angle < 0) angle += 360;   

            this.fireInfo = Utils.extend(this.fireInfo, {fire_dir: angle, fire_range: Math.round(range)});
        }
    }

    onTouchEnd(event) {   
        if (event.target == this.touchZone) {
            if (this.dpad.active) {
                this.dpad.getChildByName("indicator").setPosition(cc.Vec2.ZERO);
		    if (this.defaultDpadPos)
                this.dpad.setPosition(this.defaultDpadPos);
            }
            if (this.startPos != null) {
                this.startPos = null;
                this.setInput({direction: -1});
            }
            if (Game.isTutorial 
                && (Tutorial.tutorial_status == TUTORIAL_STATE.Tutorial_shield || Tutorial.tutorial_status == TUTORIAL_STATE.Tutorial_skill)
                && Tutorial.tutorial_sub_status == TUTORIAL_SUB_STATE.Tutorial_checkdoAction
                && Tutorial.instance.performAction == 3
                )
            {
                Tutorial.instance.performAction = 4;
            }
        }

        if (event.target == this.buttonFire)
        {
            if(!Game.USE_DYNAMIC_BOMB)
            {
                this.buttonFire.getChildByName("indicator").setScale(1);                 
                if (Game.isTutorial)
                {
                    this.setInput({fire: 1, fire_dir: 0, fire_range: Game.dynamic_range});     
                }
                else
                {
                    this.setInput({fire: 1, fire_dir: Game.instance.localPlayer.direction, fire_range: Game.dynamic_range});     
                }               
                Game.pressed_btnfire = false;  
            }
            else
            {
                this.fireInfo = Utils.extend(this.fireInfo, {fire: event.target == this.buttonFire ? 1 : 2});
                this.setInput(this.fireInfo);
                event.target.getChildByName("indicator").setPosition(cc.Vec2.ZERO);
                event.target.zIndex = this.buttonZIndex;
                this.fireCenterPos = null;
            }
        }
        else if (event.target == this.buttonSkill)
        {
            this.fireInfo = Utils.extend(this.fireInfo, {fire: event.target == this.buttonFire ? 1 : 2});
            this.setInput(this.fireInfo);
            event.target.getChildByName("indicator").setPosition(cc.Vec2.ZERO);
            event.target.zIndex = this.buttonZIndex;
            this.fireCenterPos = null;
        }
    }

  

    onButtonFire(event, customEventData) {        
        if (+customEventData == 2) { // skill
            event.target.getComponent(cc.Button).interactable = false;
        }

        if (Game.isTutorial)
            this.setInput({fire: customEventData, fire_dir: Tutorial.instance.TutorialPlayer.direction, fire_range: 100});
        else
            this.setInput({fire: customEventData, fire_dir: Game.instance.localPlayer.direction, fire_range: 100});
    }
}
