//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class HelpPage extends cc.Component {

    @property(cc.Node)
    menuPage: cc.Node = null;

    @property(cc.Node)
    apPage: cc.Node = null;

    @property(cc.Node)
    ticked: cc.Node = null;


    @property(cc.Label)
    textPage: cc.Label = null;

    @property(cc.Button)
    previous: cc.Button = null;
    
    @property(cc.Button)
    next: cc.Button = null;

    static firstLaunch_shouldShow = true;

    start () {
        this.ticked.active = PlayerRef.current.show_help < 1;
        this.node.active = HelpPage.firstLaunch_shouldShow && PlayerRef.current.show_help > 0;
        //HelpPage.firstLaunch_shouldShow = false;
    }

    onButtonsPress(event, customEventData) {
        if (customEventData == 0) { // dont show
            SoundManager.instance.playEffect("sfx_ui_select");
            let show_help = PlayerRef.current.show_help;
            PlayerRef.current.show_help = 1 - show_help;
            this.ticked.active = PlayerRef.current.show_help < 1;
        } else if (customEventData == 3) { // skip
            SoundManager.instance.playEffect("sfx_ui_back");
            this.node.active = false;
            HelpPage.firstLaunch_shouldShow = false;
        } else {
            SoundManager.instance.playEffect("sfx_ui_next");
            this.apPage.active = (customEventData == 2);
            this.menuPage.active = (customEventData == 1);
            this.textPage.string = (customEventData + "/2");
            this.previous.node.active = (customEventData == 2);
            this.next.node.active = (customEventData == 1);
        } 
    }
}
