import { BitStream } from "./BitStream";
import { LbInfo, GameData, PlayerInfo, BombInfo, ExpBubleInfo, GameInitInfo, BombExtraInfo, PlayerExtraInfo, GameInfo, DeathInfo, NotifyInfo, PlayerInput } from "./IMessageData";
// Server Only
// import { Player } from "../Player";
// import { Bomb } from "../Bomb";
// import { ExpBuble } from "../ExpBuble";
// Server Only End

// Define all message use in game and must be use for both client and server

export enum Message {
    // do not conflict each other, but in short to opt
    GameInfo = 'i',
    JoinGame = 'j',
    QueryPlayer = 'p',
    QueryBomb = 'b',
    LeaderBoard = 'l',
    InputAction = 'a',
    ServerData = 's',
    Death = 'd',
    Notify = 'n',
}

export class NetMessage {
    // packed message data to ready to send out
    protected pack(data) { return null; }
    // unpack message from recieve packed data
    protected unpack(data) { return null; }
}

export class MsgServerInfo extends NetMessage {

}

export class MsgGameInfo extends NetMessage {

    pack(data: GameInfo): ArrayBuffer {
        if (!data) return null;
        let bs = new BitStream(new ArrayBuffer(12)); 
        bs.writeUint16(data.worldW);
        bs.writeUint16(data.worldH);
        bs.writeUint16(data.playerW);
        bs.writeUint16(data.playerH);
        bs.writeUint16(data.playerSpeed);
        bs.writeUint16(data.bombRange);
        return bs.view.getArrayBuffer(0, bs.byteIndex);
    }

    unpack(data: ArrayBuffer): GameInfo {
        if (!data) return null;
        let bs = new BitStream(data);
        let result = {
            worldW: bs.readUint16(),
            worldH: bs.readUint16(),
            playerW: bs.readUint16(),
            playerH: bs.readUint16(),
            playerSpeed: bs.readUint16(),
            bombRange: bs.readUint16(),
        }
        return result;
    }
}

export class MsgQueryPlayer extends NetMessage {
    pack(player): ArrayBuffer {
        if (!player) return null;
        return player.getExtraData();
    }

    unpack(data): PlayerExtraInfo {
        if (!data) return null;
        // id | name
        // 24 | String
        let bs = new BitStream(data);
        let info = {
            id: bs.readBits(24),
            name: bs.readUTF8String(),
        }
        return info;
    }
}

export class MsgQueryBomb extends NetMessage {

    pack(bomb): ArrayBuffer {
        if (!bomb) return null;
        return bomb.getExtraData();
    }

    unpack(data): BombExtraInfo {
        if (!data) return null;
        // id | endX | endY | ownerId
        // 24 | 16   | 16   | 24
        let bs = new BitStream(data);
        let info = {
            id: bs.readBits(24),
            endX: bs.readBits(16),
            endY: bs.readBits(16),
            owner: bs.readBits(24),
        }
        return info;
    }
}

export class MsgJoinGame extends NetMessage {

    pack(data: GameInitInfo): ArrayBuffer {
        if (!data) return null;
        let bs = new BitStream(new ArrayBuffer(10));
        bs.writeBits(data.id, 24);
        bs.writeBits(data.x, 16);
        bs.writeBits(data.y, 16);
        bs.writeBits(data.startHealth, 16);
        return bs.view.getArrayBuffer(0, bs.byteIndex);
    }

    unpack(data): GameInitInfo {
        if (!data) return null;
        let bs = new BitStream(data);
        let info = {
            id: bs.readBits(24),
            x: bs.readBits(16),
            y: bs.readBits(16),
            startHealth: bs.readBits(16),
        }
        return info;
    }
}

export class MsgLeaderBoard extends NetMessage {
    pack(data: LbInfo[]): ArrayBuffer {
        if (!data) return null;
        let bs = new BitStream(new ArrayBuffer(14 * 6));
        bs.writeUint8(data.length);
        data.forEach(item => {
            bs.writeBits(item.id, 24);
            bs.writeUTF8String(item.name);
            bs.writeUint8(item.score);
        })
        return bs.view.getArrayBuffer(0, bs.byteIndex);
    }

    unpack(data): LbInfo[] {
        if (!data) return null;
        // let bs = new BitStream(data);
        // let info = [];
        // let length = bs.readUint8();
        // for (let i = 0; i < length; i++) {
        //     info.push({
        //         id: bs.readBits(24),
        //         name: bs.readUTF8String(),
        //         score: bs.readUint8()
        //     })
        // }
        let info = [];
        let arr = (data as string).split('|');
        for (let i = 0; i < +arr[0]; i++) {
            info.push({
                id: +arr[i*3 + 1],
                name: arr[i*3 + 2],
                score: +arr[i*3 + 3]
            })
        }
        return info;
    }
}

export class MsgServerData extends NetMessage {
    deserialPlayer(stream: BitStream, spec: boolean = false): PlayerInfo {
        // id | x  | y  | skin | death | action | direction | health | level | exp  | vulnerable | buff | bombable? | skillable? | cooldown? | lastInputSeq?
        // 24 | 16 | 16 | 3    | 1     | 3      | 9         | 9      | 4     | 16   | 1          | 3    | 1         | 1          | 8         | 16
        let info: PlayerInfo = {
            id: stream.readBits(24),
            x: stream.readBits(16),
            y: stream.readBits(16),
            // target_x: stream.readBits(16),
            // target_y: stream.readBits(16),
            cskin: stream.readBits(3),
            opacity: stream.readBits(8),
            death: stream.readBoolean(),
            action: stream.readBits(3),
            direction: stream.readBits(9),
            health: stream.readBits(9),
            level: stream.readBits(4),
            exp: stream.readBits(16)*5,
            vulnerable: stream.readBoolean(),
            buff: stream.readBits(3),
            buffs: []
        }
        
        if (info.buff > 0) {
            for (let i = 0; i < info.buff; i++) {
                info.buffs.push(stream.readBits(3));
            }
        }
        
        if (spec) {
            info.bombable = stream.readBoolean();
            info.skillable = stream.readBoolean();
            info.cooldown = stream.readBits(8);
            info.lastInputSeq = stream.readBits(16);
        }
        return info;
    }

    deserialBomb(stream: BitStream): BombInfo {
        // id | x  | y  | death | percent | exploded | skin
        // 24 | 16 | 16 | 1     | 7       | 1        | 3
        return {
            id: stream.readBits(24),
            x: stream.readBits(16),
            y: stream.readBits(16),
            opacity: stream.readBits(8),
            death: stream.readBoolean(),
            percent: stream.readBits(7),
            exploded: stream.readBoolean(),
            skin: stream.readBits(3), //@ for explosion skin
        }
    }

    deserialExpBuble(stream: BitStream): ExpBubleInfo {
        // id | x  | y  | death 
        // 24 | 16 | 16 | 1 
        return {
            id: stream.readBits(24),
            x: stream.readBits(16),
            y: stream.readBits(16),
            opacity: stream.readBits(8),
            death: stream.readBoolean(),
            type: stream.readBits(3),
        }
    }

    pack(data) {
        if (!data) return null;
        // Server Only
        // num-player | player... | num-bomb | bomb... | num-exp | exp...
        // let bs = new BitStream(new ArrayBuffer(1024));
        // bs.writeUint8(data.players.length);
        // data.players.forEach((player: Player, idx) => {
        //     let data: BitStream = player.getNetData();
        //     bs.writeBitStream(data, data.index, 0);
        //     if(idx == 0) {
        //         player.writeSpecData(bs);
        //     }
        // })
        // bs.writeUint8(data.bombs.length);
        // data.bombs.forEach((bomb: Bomb) => {
        //     let data: BitStream = bomb.getNetData();
        //     bs.writeBitStream(data, data.index, 0);
        // })
        // bs.writeUint8(data.bubles.length);
        // data.bubles.forEach((buble: ExpBuble) => {
        //     let data: BitStream = buble.getNetData();
        //     bs.writeBitStream(data, data.index, 0);
        // })

        // return bs.view.getArrayBuffer(0, bs.byteIndex);
        // Server Only End
        return null;
    }

    unpack(data: ArrayBuffer) {
        if (!data) return null;
        let bs = new BitStream(data);
        let info: GameData = { players: [], bombs: [], bubles: []};
        let nPlayers = bs.readUint8();
        for (let i = 0; i < nPlayers; i++) {
            info.players.push(this.deserialPlayer(bs, i == 0));
        }
        let nBombs = bs.readUint8();
        for (let i = 0; i < nBombs; i++) {
            info.bombs.push(this.deserialBomb(bs));
        }
        let nExps = bs.readUint8();
        for (let i = 0; i < nExps; i++) {
            info.bubles.push(this.deserialExpBuble(bs));
        }
        return info;
    }
}

export class MsgInputAction extends NetMessage {

    pack(data: PlayerInput) {
        if (!data) return null;
        let bs = new BitStream(new ArrayBuffer(8));
        bs.writeBoolean(data.direction != null);
        data.direction != null && bs.writeBits(data.direction, 9);
        bs.writeBoolean(data.time != null);
        data.time != null && bs.writeBits(data.time, 6);
        bs.writeBoolean(data.seq != null);
        data.seq != null && bs.writeBits(data.seq, 16);
        bs.writeBoolean(data.fire != null);
        data.fire != null && bs.writeBits(data.fire, 3);
        bs.writeBoolean(data.fire_dir != null);
        data.fire_dir != null && bs.writeBits(data.fire_dir, 9);
        bs.writeBoolean(data.fire_range != null);
        data.fire_range != null && bs.writeBits(data.fire_range, 8);
        return bs.view.getArrayBuffer(0, bs.byteIndex);
    }

    unpack(data: ArrayBuffer): PlayerInput {
        if (!data) return null;
        let bs = new BitStream(data);
        let info: any = {};
        bs.readBoolean() && (info.direction = bs.readBits(9));
        bs.readBoolean() && (info.time = bs.readBits(6));
        bs.readBoolean() && (info.seq = bs.readBits(16));
        bs.readBoolean() && (info.fire = bs.readBits(3));
        bs.readBoolean() && (info.fire_dir = bs.readBits(9));
        bs.readBoolean() && (info.fire_range = bs.readBits(8));
        if (info.direction > 360) info.direction = bs.view.getBits(1, 9, true);
        if (info.fire_dir > 360) info.direction = bs.view.getBits(39, 9, true);
        return info;
    }
}

export class MsgDeath extends NetMessage {

    pack(data: DeathInfo): ArrayBuffer {
        if (!data) return null;
        let bs = new BitStream(new ArrayBuffer(16));
        bs.writeUTF8String(data.killer);
        bs.writeUint16(data.time);
        bs.writeUint8(data.score);
        return bs.view.getArrayBuffer(0, bs.byteIndex);
    }

    unpack(data: ArrayBuffer): DeathInfo {
        if (!data) return null;
        let bs = new BitStream(data);
        return {
            killer: bs.readUTF8String(),
            time: bs.readUint16(),
            score: bs.readUint8(),
        }
    }
}

export class MsgNofity extends NetMessage {

    pack(data: NotifyInfo) {
        if (!data) return null;
        // let bs = new BitStream(new ArrayBuffer(22));
        // bs.writeASCIIString(data.kill.killer);
        // bs.writeASCIIString(data.kill.deathman);
        // return bs.view.getArrayBuffer(0, bs.byteIndex);
        // FIXME: pub/sub channel can not send binary data https://github.com/SocketCluster/sc-broker/issues/10
        // try to encode by string
        return data.kill.killer + "|" + data.kill.deathman;
    }

    unpack(data): NotifyInfo {
        if (!data) return null;
        // let bs = new BitStream(data);
        // return {
        //     kill: {
        //         killer: bs.readASCIIString(),
        //         deathman: bs.readASCIIString(),
        //     }
        // }
        let info = (data as string).split('|');
        return {
            kill: {
                killer: info[0],
                deathman: info[1],
            }
        }
    }
}