// helper class to local text

let i18n = require('i18n');
export default class Localization {

    /**
     * Get translate text for key
     * @param key text key
     */
    static get(key: string): string {
        let text = i18n.t(key);
        if(text == "") text = "N/A";
        return text;
    }

    /**
     * Set game language to new lang
     * It also set the save lang to new lang if valid
     * @param lang 
     */
    static setLanguage(lang: string) {
        if(i18n.init(lang)) {
            // reload localize item on screen
            let rootNodes = cc.director.getScene().children;
            // walk all nodes with localize label and update
            let allLocalizedLabels = [];
            for (let i = 0; i < rootNodes.length; ++i) {
                let labels = rootNodes[i].getComponentsInChildren('LabelLocalized');
                Array.prototype.push.apply(allLocalizedLabels, labels);
            }
            for (let i = 0; i < allLocalizedLabels.length; ++i) {
                let label = allLocalizedLabels[i];
                label.updateLabel();
            }
        }
    }

    /**
     * get the current save language
     */
    static getCurrentLanguage() {
        return i18n.getSaveLang();
    }
}