// this class use as a mail box meassage to exchange message (ex: send msg with 2 scene)
// if we send the same message to same people, the lastest will override the others

export default class TransportMessage {

    private static _instance: TransportMessage = null;
    public static get instance(): TransportMessage {
        if(this._instance == null) {
            this._instance = new TransportMessage();
        }

        return this._instance;
    }

    private _messages: {[key: string]: {[key: string]: string}} = {};

    public send(to: string, message: string, content: string = "") {
        if(!this._messages[to]) this._messages[to] = {};

        this._messages[to][message] = content;
    }

    public get(name: string, message: string, deleteAfterRead: boolean = true): string|null {

        let messageContent = null;
        if(this._messages[name] && this._messages[name][message] != null) {
            messageContent = this._messages[name][message].slice(0, this._messages[name][message].length);
            if (deleteAfterRead)
                delete this._messages[name][message];
        }   
        return messageContent;
    }
}