import { LbInfo } from "../multiplayer/shared/IMessageData";
import Game from "../game";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Leaderboard extends cc.Component {

    @property(cc.Prefab)
    entryPrefab: cc.Prefab = null;

    @property(cc.Node)
    content: cc.Node = null;

    @property({ type: [cc.SpriteFrame] })
    ranks: cc.SpriteFrame[] = [];

    start() {
    }

    setData(data: LbInfo[]) {
        let i = 0;
        //console.log("setData: length: " + data.length); 
        data.forEach((item, idx) => {
            let rank = idx + 1;
            let entry = this.content.children[i++];
            if (!entry) {
                entry = cc.instantiate(this.entryPrefab);
                entry.getChildByName("rank-icon").addComponent(cc.Sprite);
                this.content.addChild(entry);
            }
            if (rank <= 3) {
                entry.getChildByName("rank-icon").active=true;
                entry.getChildByName("rank-icon").getComponent(cc.Sprite).spriteFrame = this.ranks[rank - 1];                
                entry.getChildByName("rank").active = false;
            } else {
                entry.getChildByName("rank-icon").active=false;
                entry.getChildByName("rank").active = true;
                entry.getChildByName("rank").getComponent(cc.Label).string = "" + rank + ".";
            }
            entry.getChildByName("score").getComponent(cc.Label).string = "" + item.score;

            let name = entry.getChildByName("name");
            Game.instance.localPlayer.id == item.id ? name.color = cc.Color.GREEN : name.color = cc.Color.WHITE;
            name.getComponent(cc.Label).string = "" + item.name;
        });

        while (i < this.content.childrenCount) {
            let entry = this.content.children[i++];
            entry.removeFromParent();
        }
    }
}