    resetTutorialState()
    {
        Tutorial.tutorial_status = TUTORIAL_STATE.Tutorial_move;
        Tutorial.tutorial_sub_status = TUTORIAL_SUB_STATE.Tutorial_default;
        Tutorial.instance.state_comepleted = -1;
        Tutorial.instance.performAction = 0;
    }

    start() {
        if (!Loader.loaded) return;      // prevent run when loader init
        
        if (!Game.isTutorial)
        {
            this.gotoMainMenu();
            return;
        }
        Popup.instance.setTarget(this.node.getChildByName("uiRoot")); //set target

        this.collisionManager = cc.director.getCollisionManager();
        this.collisionManager.enabled = true;
        //this.collisionManager.enabledDebugDraw = true;
        this.collisionManager.enabledDebugDraw = false;
        
        this.initEntities();
        
        // generate popup
        
        if (!this.block_skillpad)
            this.block_skillpad = cc.find("/Canvas/uiRoot/skill-pad/block_input");
        if (!this.block_touchzone)
            this.block_touchzone = cc.find("/Canvas/uiRoot/touchZone/block_input");
        if (!this.block_firepad)
            this.block_firepad = cc.find("/Canvas/uiRoot/fire-pad/block_input");

        if (!this.btn_firepad)
            this.btn_firepad = cc.find("/Canvas/uiRoot/fire-pad");
        if (!this.btn_skillpad)
            this.btn_skillpad = cc.find("/Canvas/uiRoot/skill-pad");
        if (!this.btn_dpad)
            this.btn_dpad = cc.find("/Canvas/uiRoot/d-pad");
        
        this.setlockControl(true);
        // let tmp:cc.Node = cc.find("/Canvas/uiRoot/skill-pad/indicator");
        // let m_sprite = tmp.getComponent(cc.Sprite);
        // m_sprite.
        SoundManager.instance.playMusic("m_ap");
        
    }

    update(dt)
    {
        if (!Loader.loaded || !Game.isTutorial) return;      // prevent run when loader init

        if (CameraControl.instance.getTarget() == null && this.worldMap.getChildByName("Main MC"))
        {
            //cc.log ("Nhan --> find Maain MC ok");
            CameraControl.instance.setTarget(this.TutorialPlayer.node);
        }

        // cc.log ("Nhan --> this.state_comepleted:" + this.state_comepleted);
        // cc.log ("Nhan --> Tutorial.tutorial_status:" + Tutorial.tutorial_status);

        if (this.state_comepleted == Tutorial.tutorial_status)
        {
            this.updateTutorialStatus();
        }
        this.UpdateTutorial_Step();

        // For update auto move for BOT
        if (this.BOTplayer.action != PlayerAction.Fire && this.BOTplayer.bombable)
            this.BOTplayer.setTargetPos(this.TutorialPlayer.node.position);

    }


