	onCollisionEnter(other, self) {
        //console.log("Player Currently colliding");
        if (Game.isTutorial)
        {
            if (other.tag == 2)
            {
                if ( this.effectsNode.children[0].active )
                {
                    //console.log("Player colliding --> 2");
                    let name = this.fx_shield.currentClip.name;
                    //console.log("Player colliding --> this.fx_shield.currentClip.name = " + name);
                    if (name == "start-invulnerable")
                        this.effectsNode.children[0].active = false;
                    else
                        this.numofBombReceive++;
                }
                else
                {
                    this.health -= ConfigGame.BOMB_BASE_DAMGE;
                    this.healthBar.updateHealth(this.level, this.health);
                }
            }
        }

    }
    onCollisionExit(other,self){
        //console.log("Player Done colliding");
    }

    init(data: { id, x, y, name, health, level, exp, cskin }) {
        super.init(data);
        //this.initClips(data.cskin);
        this.animation = this.sprite.getComponent(cc.Animation);
        this.animation.on("finished", this.onAnimationEvent, this);


        //effect
        this.fx_shield = this.effectsNode.children[0].getComponent(cc.Animation);
        this.fx_shield.play("protector");
        this.fx_levelUp = this.effectsNode.children[1];
        this.fx_buff = this.effectsNode.children[2].getComponent(cc.Sprite);
        this.fx_warn = this.effectsNode.children[3];
        
        //player info
        this.playerName.string = data.name;
        this.cskin = data.cskin;
        this.health = data.health;
        this.level = data.level;
        this.exp = data.exp;
        //this.healthBar.updateHealth(Player.getHealth(this.level), this.health);
        this.healthBar.updateHealth(this.level, this.health);
        this.setPosition(data.x, data.y);
        this.setStatus(0);
        // Nhan add
        this.setBombskin(data.id);
    }

