    setPlayerName(name: string) {
        this.playerName.string = name;
    }

    getPlayerName() {
        return this.playerName.string;
    }

    setPosition(x: number, y: number) {
        if (this.death) return;

        this.node.setPosition(x, y);
    }

    setServerData(data: PlayerInfo) {
        this.cskin = data.cskin;
        this.updatePlayerInfo(data.x, data.y, data.opacity, data.health, data.level, data.exp, data.death, data.vulnerable, data.bombable, data.skillable, data.buffs);
        this.updateAction(data.action, data.direction);
        //this.node.opacity = data.opacity;
        // Nhan add
    }

    updatePlayerInfo(x: number, y: number, opacity: number, health: number, level: number, exp: number, death: boolean, vulnerable: boolean, bombable: boolean, skillable: boolean, buffs: number[]) {
        super.setStateData({ x: x, y: y });
        this.death = death;
        //this.healthBar.updateHealth(Player.getHealth(level), health);
        if (this.level < level) { //leveup
            this.level = level;
            this.fx_levelUp.active = true;
            setTimeout(() => {
                this.fx_levelUp.active = false;
            }, 2000);
        }
        
        this.healthBar.updateHealth(level, health);
        this.health = health;
        this.exp = exp;
        this.bombable = bombable;
        this.skillable = skillable;
        this.effects = [...buffs];

        if (this.invulnerable) {
            if (vulnerable) {
                this.invulnerable = false;
                this.fx_shield.node.active = false;
                this.fx_shield.play("start-invulnerable");
            }
        } else {
            this.fx_shield.node.active = this.hasStatus(PlayerStatus.Block);
        }
        
        this.node.opacity = opacity;
        if (this.hasStatus(PlayerStatus.Invisible)) {
            this.node.opacity = (this == Game.instance.localPlayer) ? 100 : 0;
        } 

        let showEffect = false;
        [PlayerStatus.ExpX2, PlayerStatus.DefX2, PlayerStatus.DmgX2].forEach(eff => {
            if (this.hasStatus(eff)) {
                showEffect = true;
                this.fx_buff.spriteFrame = this.buffFrames[eff - 2];
            }
        })
        this.fx_buff.node.active = showEffect;
        

        // this.status.removeAllChildren();
        // let headStt = [PlayerStatus.DmgX2, PlayerStatus.DefX2, PlayerStatus.ExpX2]
        // headStt.forEach(stt => {
        //     if (this.hasStatus(stt)) {
        //         let effect = cc.instantiate(this.statusItem);
        //         effect.getComponent(cc.Sprite).spriteFrame = this.buffFrames[stt - 2];
        //         this.status.addChild(effect);        
        //     }
        // })
        // this.status.active = (this.status.childrenCount > 0);
    }

    setStatus(top: number, localPlayer: boolean = false) {  // rank = 0: not in top
        this.fx_warn.active = !localPlayer && top > 0;
        // this.rank.spriteFrame = this.rankFrames[top];
        this.rank.node.active = top > 0;        
        var anim = this.rank.getComponent(cc.Animation);
        if(top > 0)
        {
            anim.play(this.rank_anim[top]);
        }
        else
            anim.stop();
    }

    hasStatus(status: number) {
        return this.effects.length > 0 && this.effects.indexOf(status) >= 0;
    }

    // Nhan add
	
	updateImpactEffects()
    {
        let showEffect = false;
        [PlayerStatus.ExpX2, PlayerStatus.DefX2, PlayerStatus.DmgX2].forEach(eff => {
            if (this.hasStatus(eff)) {
                showEffect = true;
                this.fx_buff.spriteFrame = this.buffFrames[eff - 2];
            }
        })
        this.fx_buff.node.active = showEffect;
    }
	
