// use this super class to present server sync object

const { ccclass, property } = cc._decorator;

@ccclass
export default class NetComponent extends cc.Component {

    static EVENT_NET_ENTITY_DEATH = "net_entity_death";

    private _cid: number = 0; //@ change to _cid to avoid error 1510Illegal target which doesn't have uuid or instanceId.
    protected x: number = 0;
    protected y: number = 0;
    protected death: boolean = false;
    private lastStateTime: number = 0;      // last time receive data from server, out of time -> treat object death
    private isDestroying: boolean = false;

    get id() { return this._cid; }

    init(data: {id, x, y}) {
        this._cid = data.id;
        this.x = data.x;
        this.y = data.y;
        this.lastStateTime = Date.now();
    }

    setStateData(data: {x, y}) {
        this.x = data.x || this.x;
        this.y = data.y || this.y;
        this.lastStateTime = Date.now();
    }

    isDeath() {
        return this.death || (Date.now() - this.lastStateTime > 3000);
    }

    setDeath(death: boolean) {
        this.death = death;
    }

    onDeath(timeout?: boolean) {}

    lateUpdate() {
        if(this.isDeath() && !this.isDestroying) {
            this.isDestroying = true;
            if(this.death) {
                this.onDeath();
            } else {
                this.onDeath(true);
            }
        }
    }

}