import Input, { InputData } from "./input";
import Game from "./game";
const { ccclass, property } = cc._decorator;

// keyboard controller support

@ccclass
export default class KeyInput extends cc.Component {

    @property(cc.Node)
    dpad: cc.Node = null;

    private keys = {};      // key status
    private preAngle = -1;

    start() {
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
    }

    update() {
        this.updateMove();
    }

    setInput(data: InputData) {
        Input.instance.set(data, 0);        // touch weight: 1, keyboard weight: 0
    }

    onKeyUp(event: cc.Event.EventKeyboard) {
        let code = event.keyCode;
        code == cc.macro.KEY.up ? code = cc.macro.KEY.w : null;
        code == cc.macro.KEY.down ? code = cc.macro.KEY.s : null;
        code == cc.macro.KEY.left ? code = cc.macro.KEY.a : null;
        code == cc.macro.KEY.right ? code = cc.macro.KEY.d : null;
        this.keys[code] = 0;
        // fire
        if (code == cc.macro.KEY.space) {
            if(!Game.USE_DYNAMIC_BOMB)
            {
                Game.pressed_btnfire = false;
            }
            this.setInput({ fire: 1 , fire_dir: Game.instance.localPlayer.direction, fire_range: Game.dynamic_range});
        } else if ( code == cc.macro.KEY.shift ) {
            this.setInput({ fire: 2 , fire_dir: Game.instance.localPlayer.direction, fire_range: 100});
        }
        
        
    }

    onKeyDown(event: cc.Event.EventKeyboard) {
        let code = event.keyCode;
        code == cc.macro.KEY.up ? code = cc.macro.KEY.w : null;
        code == cc.macro.KEY.down ? code = cc.macro.KEY.s : null;
        code == cc.macro.KEY.left ? code = cc.macro.KEY.a : null;
        code == cc.macro.KEY.right ? code = cc.macro.KEY.d : null;
        if(!Game.USE_DYNAMIC_BOMB && code == cc.macro.KEY.space) 
        {
            Game.pressed_btnfire = true;
        }
        this.keys[code] = 1;
    }

    updateMove() {
        // check move
        let angle = this.getMoveDirection();

        if (angle > -1) {
            this.preAngle = angle;
            this.setInput({ direction: angle });
            //@ update dpad
            if (this.dpad.active) {
                let rad = angle * Math.PI / 180;
                let x = Math.round(Math.cos(rad) * 100);
                let y = Math.round(Math.sin(rad) * 100);
                this.dpad.getChildByName("indicator").setPosition(x, y);
            }
        } else if (this.preAngle != -1) {        // stop
            this.setInput({ direction: -1 });
            this.preAngle = -1;
            if (this.dpad.active) {
                this.dpad.getChildByName("indicator").setPosition(cc.Vec2.ZERO);
            }
        }
    }

    getMoveDirection(): number {
        let moveDirection = -1;
        if (this.keys[cc.macro.KEY.w]) {
            if (this.keys[cc.macro.KEY.a]) moveDirection = 135;
            else if (this.keys[cc.macro.KEY.d]) moveDirection = 45;
            else moveDirection = 90;
        } else if (this.keys[cc.macro.KEY.d]) {
            if (this.keys[cc.macro.KEY.w]) moveDirection = 45;
            else if (this.keys[cc.macro.KEY.s]) moveDirection = 360 - 45;
            else moveDirection = 0;
        } else if (this.keys[cc.macro.KEY.s]) {
            if (this.keys[cc.macro.KEY.d]) moveDirection = 360 - 45;
            else if (this.keys[cc.macro.KEY.a]) moveDirection = 360 - 135;
            else moveDirection = 360 - 90;
        } else if (this.keys[cc.macro.KEY.a]) {
            if (this.keys[cc.macro.KEY.w]) moveDirection = 135;
            else if (this.keys[cc.macro.KEY.s]) moveDirection = 360 - 135;
            else moveDirection = 180;
        }
        return moveDirection;
    }

}