// import FirebaseInst from "./libs/firebase-inst";
import EventManager from "./core/event-manager";

export enum ControlType {
    DPad = 0,
    Touch = 1,
}

export enum DesignType {
    Fire = 0,
    Ice, 
    Grass
}

// this singleton class keep all player ref

export default class PlayerRef {

    public initialized: boolean = false;

    private static _current: PlayerRef = null;
    public static get current(): PlayerRef {
        if(this._current == null) {
            this._current = new PlayerRef();
            this._current.init();
            // get/init anon user form firebase
            // this._current.authFirebase();
        }

        return this._current;
    }
    
    // private authFirebase() {
    //     if(FirebaseInst.app == null) FirebaseInst.init();

    //     FirebaseInst.app.auth().onAuthStateChanged(user => {
    //         if(user) {
    //             PlayerRef.current.setId(user.uid);
    //             cc.log("Login anon success: " + user.uid);
    //             EventManager.instance.dispatch('PlayerRef-init', 'success');
    //         }
    //     })

    //     FirebaseInst.app.auth().signInAnonymously().catch(error => {
    //         cc.warn("Authentication anon error: " + error.message);
    //     })
    // }

    init() {
        // this.clear();
        if (!this.name) this.name = "Default";
        this.control = cc.sys.isMobile ? ControlType.DPad : ControlType.Touch;
        if (!this.user) this.user = DesignType.Fire;
        if (!this.bomb) this.bomb = DesignType.Fire;
        if (!this.map) this.map = DesignType.Fire;
        if (!this.streak) this.streak = 0;
        if (!this.alive) this.alive = 0;
        if (this.first_launch_step == null) this.first_launch_step = 0;
        // if (this.last_server_selection == undefined) this.last_server_selection = 0;
        if (this.istutorial == null) this.istutorial = 1;
        if (this.isRabitTutorial == null) 
        {
            this.isRabitTutorial = 1;
        }
        if (this.isPenguinTutorial == null)
        {
            this.isPenguinTutorial = 1;
        }
        if (this.isLeafTutorial == null) 
        {
            this.isLeafTutorial = 1;
        }
        if (!this.show_help) this.show_help = 1;
        if (!this.crediental) this.crediental ='';
        EventManager.instance.dispatch('PlayerRef-init', 'success');
        this.initialized = true;
    }

    clear() {
        cc.sys.localStorage.clear();
    }

    updateLocalRecord(streak: number, time: number) {
        if (!this.streak || this.streak < streak) {
            this.streak = streak;
        }

        if (!this.alive || this.alive < time) {
            this.alive = time;
        }
    }

    //////////////////// property ///////////////////////
    public get name() { return cc.sys.localStorage.getItem('user-name') }
    public set name(name: string) { cc.sys.localStorage.setItem('user-name', name) }

    public get control() { return cc.sys.localStorage.getItem('user-control-type') }
    public set control(type: number) { cc.sys.localStorage.setItem('user-control-type', type) };

    public get user() { return cc.sys.localStorage.getItem('user-skin') }
    public set user(type: number) { cc.sys.localStorage.setItem('user-skin', type) };

    public get bomb() { return cc.sys.localStorage.getItem('bomb-skin') }
    public set bomb(type: number) { cc.sys.localStorage.setItem('bomb-skin', type) };

    public get map() { return cc.sys.localStorage.getItem('user-map') }
    public set map(type: number) { cc.sys.localStorage.setItem('user-map', type) };

    public get streak() { return cc.sys.localStorage.getItem('user-killstreak') }
    public set streak(kill: number) { cc.sys.localStorage.setItem('user-killstreak', kill) };

    public get alive() { return cc.sys.localStorage.getItem('user-time-alive') }
    public set alive(time: number) { cc.sys.localStorage.setItem('user-time-alive', time) };

    public get show_help() { return cc.sys.localStorage.getItem('user-show-help') }
    public set show_help(show: number) { cc.sys.localStorage.setItem('user-show-help', show) };
    public get istutorial() { return cc.sys.localStorage.getItem('user-is-Tutorial') }
    public set istutorial(is_tutorial: number) { cc.sys.localStorage.setItem('user-is-Tutorial', is_tutorial) };

    public get isRabitTutorial() { return cc.sys.localStorage.getItem('user-is-RabitTutorial') }
    public set isRabitTutorial(is_tutorial: number) { cc.sys.localStorage.setItem('user-is-RabitTutorial', is_tutorial) };

    public get isPenguinTutorial() { return cc.sys.localStorage.getItem('user-is-PenguinTutorial') }
    public set isPenguinTutorial(is_tutorial: number) { cc.sys.localStorage.setItem('user-is-PenguinTutorial', is_tutorial) };
    
    public get isLeafTutorial() { return cc.sys.localStorage.getItem('user-is-LeafTutorial') }
    public set isLeafTutorial(is_tutorial: number) { cc.sys.localStorage.setItem('user-is-LeafTutorial', is_tutorial) };

    public get crediental() { return cc.sys.localStorage.getItem('user-crediental') }
    public set crediental(user_cred: string) { cc.sys.localStorage.setItem('user-crediental', user_cred) };

    public get email() { return cc.sys.localStorage.getItem('user-email') }
    public set email(user_email: string) { cc.sys.localStorage.setItem('user-email', user_email) };

    public get first_launch_step() { return cc.sys.localStorage.getItem('first-launch') }
    public set first_launch_step(step: number) { cc.sys.localStorage.setItem('first-launch', step) };

    public get last_server_selection() { return cc.sys.localStorage.getItem('last-server') }
    public set last_server_selection(server_index: number) { cc.sys.localStorage.setItem('last-server', server_index) };

    // public get last_server_host() { return cc.sys.localStorage.getItem('last-server-host') }
    // public set last_server_host(step: string) { cc.sys.localStorage.setItem('last-server-host', step) }

    // public get last_server_port() { return cc.sys.localStorage.getItem('last-server-port') }
    // public set last_server_port(step: number) { cc.sys.localStorage.setItem('last-server-port', step) }

    // public get last_server_secure() { return cc.sys.localStorage.getItem('last-server-secure') }
    // public set last_server_secure(step: boolean) { cc.sys.localStorage.setItem('last-server-secure', step) }
}