import { scformatter } from './SCFormatter';       // default sc codec
import { JsonPacker } from './JsonPacker';

export class SCCustomCodec {

    static packer: JsonPacker = new JsonPacker();

    private compressPublishPacket(object) {
        if (object.event != '#publish' || object.data == null) {
            return;
        }

        object.publish = {
            channel: object.data.channel,
            data: object.data.data
        };

        if (object.cid != null) {
            object.publish.cid = object.cid;
        }

        delete object.event;
        delete object.data;
        delete object.cid;
    }

    private decompressPublishPacket(object) {
        if (object.publish == null) {
            return;
        }

        object.event = '#publish';
        object.data = {
            channel: object.publish.channel,
            data: object.publish.data
        }
        if (object.publish.cid != null) {
            object.cid = object.publish.cid;
        }
        delete object.publish;
    }

    private compressEmitPacket(object) {
        if (object.event == null) {
            return;
        }

        object.emit = {
            event: object.event,
            data: object.data
        }
        if (object.cid != null) {
            object.emit.cid = object.cid;
        }
        delete object.event;
        delete object.data;
        delete object.cid;
    }

    private decompressEmitPacket(object) {
        if (object.emit == null) {
            return;
        }

        object.event = object.emit.event;
        object.data = object.emit.data;
        if (object.emit.cid != null) {
            object.cid = object.emit.cid;
        }
        delete object.emit;
    }

    private compressResponsePacket(object) {
        if (object.rid == null) {
            return;
        }

        object.response = {
            rid: object.rid,
            data: object.data
        };

        delete object.rid;
        delete object.error;
        delete object.data;
    }

    private decompressResponsePacket(object) {
        if (object.response == null) {
            return;
        }

        object.rid = object.response.rid;
        object.data = object.response.data;
        delete object.response;
    }

    private clonePacket(object) {
        let clone = {};
        for (let i in object) {
            if (object.hasOwnProperty(i)) {
                clone[i] = object[i];
            }
        }
        return clone;
    }

    private formatSinglePacket(object) {
        object = this.clonePacket(object);
        this.compressPublishPacket(object);
        this.compressEmitPacket(object);
        this.compressResponsePacket(object);
        return object;
    }

    private deformatSinglePacket(object) {
        this.decompressEmitPacket(object);
        this.decompressPublishPacket(object);
        this.decompressResponsePacket(object);
    }

    encode(object) {        
        if (object && (
            object.data instanceof ArrayBuffer ||
            (object.data && object.data.data instanceof ArrayBuffer)
        )) {            
            return SCCustomCodec.packer.pack(this.formatSinglePacket(object));
        }

        return scformatter.encode(object);
    }

    decode(input) {
        if (input instanceof ArrayBuffer || (typeof Buffer !== 'undefined' && input instanceof Buffer)) {
            let object = SCCustomCodec.packer.unpack(input);
            this.deformatSinglePacket(object);
            return object;
        }

        return scformatter.decode(input);
    }
}